import axios from "axios";

const state = {
  cocktails: [],
};

const getters = {
  getCocktails(state) {
    return state.cocktails;
  },
};

const mutations = {
  newCocktail(state, payload) {
    state.cocktails.push(payload);
    return "success";
  },
  setCocktails(state, data) {
    state.cocktails = data;
  },
};

const actions = {
 
  getCocktailsList({ commit, rootState }) {
    axios
      .get("/cocktails", rootState.authentication.accessTokenConfig)
      .then((res) => {
        commit("setCocktails", res.data);
      })
      .catch((error) => {
        console.log(error.response);
      });
  },
  setCocktail({ commit, dispatch, rootState }, data) {
  
    axios.post("/cocktails/add", data.cocktail, rootState.authentication.accessFileTokenConfig )
    .then(res => {
        if(res.status == 201){
          dispatch('setCocktailImage',data.image)
        }
      })
      .catch(error => {
        commit('setServerMessage', { message:error.response.data.message, heading:'New cocktail adding failed!', active:true, type:'error' }, { root: true })
        console.log(error.response);
      })
    },
    setCocktailImage({ commit, rootState, dispatch }, data) {

      let formData = new FormData();
      formData.append('image',data.file)
      axios.post(`/cocktails/image/${data.name}`, formData, rootState.authentication.accessFileTokenConfig )
      .then(res => {
        if(res.status == 201){
          commit('setServerMessage', { message:res.data.message, heading:'New cocktail added', active:true, type:'success' }, { root: true })
          dispatch('getCocktailsList')
        }
    })
    .catch(error => {
        commit('setServerMessage', { message:error.response.data.message, heading:'New cocktail added image adding failed!', active:true, type:'error' }, { root: true })
        console.log(error.response);
    })
  },
};

export default {
  state,
  mutations,
  actions,
  getters,
};
