import axios from "axios";
import router from "../../router";


const state = {
  access_token: null,
  refresh_token: null,
  user_id: null,
  user_email: null,
  user_name: null,
  user_claims: null,
  accessTokenConfig: null,
  refreshTokenConfig: null,
  accessFileTokenConfig: null,
  message: { msg:null, type:null, dialog:false, heading:null },
  logoutDialog:false
};

const getters = {
  User(state) {
    return { name: state.user_name, email: state.user_email };
  },

  UserClaims(state) {
    return state.user_claims;
  },

  isAuthenticated(state) {
    return state.access_token !== null && state.refresh_token !== null;
  },

  authMessage(state) {
    return state.message;
  },

  accessTokenConfig(state) {
    return state.accessTokenConfig;
  },
  refreshTokenConfig(state) {
    return state.refreshTokenConfig;
  },
  logoutDialog(state){
    return state.logoutDialog
  }
  
};

const mutations = {

  authUser(state, data) {
    state.access_token = data.access_token;
    state.refresh_token = data.refresh_token;
    state.user_id = data.identity;
    state.user_name = data.user_name;
    state.user_email = data.user_email;
    state.user_claims = data.user_claims;
  },

  storeUser(state, User) {
    state.User = User;
  },

  clearAuthData(state) {
    state.access_token = null;
    state.refresh_token = null;
    state.user_id = null;
    state.user_name = null;
    state.user_email = null;
    state.user_claims = null;
  },

  clearMessage(state) {
    state.message = { msg:null, type:null, dialog:false, heading:null };
  },

  setMessage(state, data) {
    state.message = data;
  },

  setAccessTokenConfig(state){
    state.accessTokenConfig = {
      headers: { Authorization: `Bearer ${state.access_token}` }
    };

    state.accessFileTokenConfig = {
      headers: { 
        Authorization: `Bearer ${state.access_token}`,
        Accept: "multipart/form-data"  
      }
    };
  },
  setRefreshTokenConfig(state){
    state.refreshTokenConfig = {
      headers: { Authorization: `Bearer ${state.refresh_token}` }
    };
  },
  openLogoutDialog(state){
    state.logoutDialog = true
  },
  refreshToken(state, token){
    state.refreshToken = token
  },
  closeLogoutDialog(state){
    state.logoutDialog = false
  }

};

const actions = {
  setLogoutTimer ({commit}, expirationTime) {
   
    setTimeout(() => {
      commit('openLogoutDialog')
    }, expirationTime * 1000)
  },
  login({ commit, dispatch }, authData) {
    axios.post("/login", authData)
      .then(res => {
        if (res.status == 200) {
          const now = new Date()
          const expirationDate = new Date(now.getTime() + res.data.expires * 1000)
          const decoded_access_token = JSON.parse(
            atob(res.data.access_token.split(".")[1])
          );
          localStorage.setItem("access_token", res.data.access_token);
          localStorage.setItem("refresh_token", res.data.refresh_token);
          localStorage.setItem("user_id", decoded_access_token.identity);
          localStorage.setItem("user_name", res.data.user_name);
          localStorage.setItem("user_email", res.data.user_email);
          localStorage.setItem("User_claims", decoded_access_token.user_claims);
          localStorage.setItem('expirationDate', expirationDate)
          commit("authUser", {
            access_token: res.data.access_token,
            refresh_token: res.data.refresh_token,
            user_id: decoded_access_token.identity,
            user_name: res.data.user_name,
            user_email: res.data.user_email,
            user_claims: decoded_access_token.user_claims
          });
          commit('setAccessTokenConfig');
          commit('setRefreshTokenConfig');
          dispatch('setLogoutTimer', res.data.expires)
          if (decoded_access_token.user_claims.is_admin)
            router.replace("/admin");
          else
            router.replace("/user") 
        }
        
      })
      .catch(error => {
        console.log(error.response);
        let mdata = { msg:error.response.data.message,type:"failed", dialog: true, heading:'Authentication Failed'};
        commit("setMessage", mdata);
      });
  },
  tryAutoLogin ({commit, dispatch}) {
    const access_token = localStorage.getItem('access_token');
    const refresh_token = localStorage.getItem('refresh_token');
    if (!access_token && !refresh_token) {
      return
    }
    const expirationDate = new Date(localStorage.getItem('expirationDate'))
    const now = new Date()
    if (now >= expirationDate) {
      return
    } 
    commit("authUser", {
      access_token: localStorage.getItem('access_token'),
      refresh_token: localStorage.getItem('refresh_token'),
      user_id: localStorage.getItem('user_id'),
      user_name: localStorage.getItem('user_name'),
      user_email: localStorage.getItem('user_email'),
      user_claims: localStorage.getItem('user_claims')
    });
    const time = (expirationDate.getTime() - now.getTime())/1000 
    dispatch('setLogoutTimer', time )
    commit('setAccessTokenConfig');
    commit('setRefreshTokenConfig');
  },
  logout({ commit }) {
    commit("clearAuthData");
    localStorage.clear()
  },
  reLogin({commit, getters, dispatch}, ){
    axios.post("/refresh",{},getters.refreshTokenConfig)
    .then((res) => {
      commit("refreshToken",res.data.access_token );
      const now = new Date()
      const expirationDate = new Date(now.getTime() + res.data.expires * 1000)
      localStorage.setItem("access_token", res.data.access_token);
      localStorage.setItem('expirationDate', expirationDate)
      dispatch('setLogoutTimer', res.data.expires)
    }).catch((error)=>{
      console.log(error.response)
    })
  },
  register({ commit }, data){
    axios.post("/register", data)
    .then( res => {
      if (res.status == 201) {   
        let mdata = { msg:res.data.message,type:"success", dialog: true, heading:'Registration Successful' };
        commit("setMessage", mdata);
      }
    })
    .catch( error => {
      console.log(error.response)
      if(error.response.status!=201){
        let msg_data = { msg:error.response.data.message,type:"failed", dialog: true, heading:'Registration Failed' };
        commit("setMessage", msg_data);
      }
        
    })
  },
  resetMessage({ commit }){
    commit("clearMessage");
  },
  closeLogoutDialog({commit}){
    commit('closeLogoutDialog')
  },
  resetRequest({ commit }, data){
    axios.post(`/request/password-reset/${data.email}`)
    .then( res => {
      if (res.status == 201) {   
        let mdata = { msg:res.data.message,type:"success", dialog: true, heading:'Request Successful' };
        commit("setMessage", mdata);
      }
    })
    .catch( error => {
      console.log(error.response)
      if(error.response.status!=201){
        let msg_data = { msg:error.response.data.message,type:"failed", dialog: true, heading:'Request Failed!' };
        commit("setMessage", msg_data);
      }
        
    })
  },
  resetPassword({ commit }, data){
    axios.post(`/password/reset`,data)
    .then( res => {
      if (res.status == 201) {   
        let mdata = { msg:res.data.message,type:"success", dialog: true, heading:'Password updation successful' };
        commit("setMessage", mdata);
      }
    })
    .catch( error => {
      console.log(error.response)
      if(error.response.status!=201){
        let msg_data = { msg:error.response.data.message,type:"failed", dialog: true, heading:'Password updation failed!' };
        commit("setMessage", msg_data);
      }
        
    })
  },
};

export default {
  state,
  mutations,
  actions,
  getters
};
