import axios from "axios";


const state = {
    devices: [] ,
    products: [],
    productModels: [],
    bartinys:[],
}

const getters = {
    Devices(state) {
        return state.devices;
    },
    Products(state) {
        return state.products
    },
    ProductModels(state) {
        return state.productModels
    },
    Bartinys(state) {
        return state.bartinys
    }
}

const mutations = {
    setDevices(state, data){
        state.devices = data
    },
    setProducts(state, data){
        state.products = data
    },
    setProductModels(state, data){
        state.productModels = data
    },
    setBartinys(state, data) {
        state.bartinys = data
    }
}

const actions = {
    getDevicesList({commit, rootState}){
        axios.get("/devices", rootState.authentication.accessTokenConfig)
        .then(res => {
            if(res.status == 200)
                commit('setDevices', res.data)
        })
        .catch(error => {
            console.log(error.response);
        })
    },
    getBartinysList({commit, rootState}){
        axios.get("/bartiny", rootState.authentication.accessTokenConfig)
        .then(res => {
            if(res.status == 200)
                commit('setBartinys', res.data)
        })
        .catch(error => {
            console.log(error.response);
        })
    },
    getProductsList({commit, rootState}){
        axios.get("/products", rootState.authentication.accessTokenConfig)
        .then(res => {
            if(res.status == 200)
                commit('setProducts', res.data)
        })
        .catch(error => {
            console.log(error.response);
        })
    },
    getProductModelsList({commit, rootState}){
        axios.get("/products/models", rootState.authentication.accessTokenConfig)
        .then(res => {
            if(res.status == 200)
                commit('setProductModels', res.data)
        })
        .catch(error => {
            console.log(error.response);
        })
    },
    setProduct({ commit, dispatch, rootState}, data){
        console.log(data)
        axios.post("/products/add", data, rootState.authentication.accessTokenConfig )
        .then(res => {
            if(res.status == 201){
                commit('setServerMessage', { message:res.data.message, heading:'New Product Created', active:true, type:'success' }, { root: true })
                dispatch('getProductsList')
            }
        })
        .catch(error => {
            commit('setServerMessage', { message:error.response.data.message, heading:'Product Creation Failed!', active:true, type:'error' }, { root: true })
            console.log(error.response);
        })
    },
    setProductModel({ commit, dispatch, rootState}, data){
        console.log(data)
        axios.post("/products/models/add", data, rootState.authentication.accessTokenConfig )
        .then(res => {
            if(res.status == 201){
                commit('setServerMessage', { message:res.data.message, heading:'New Product Model Created', active:true, type:'success' }, { root: true })
                dispatch('getProductModelsList')
            }
        })
        .catch(error => {
            commit('setServerMessage', { message:error.response.data.message, heading:'Product Model Creation Failed!', active:true, type:'error' }, { root: true })
            console.log(error.response);
        })
    },
    setDevice({ commit, dispatch, rootState}, data){
        console.log(data)
        axios.post("/devices/create", data, rootState.authentication.accessTokenConfig )
        .then(res => {
            if(res.status == 201){
                commit('setServerMessage', { message:res.data.message, heading:'New Device Created', active:true, type:'success' }, { root: true })
                dispatch('getDevicesList')
            }
        })
        .catch(error => {
            commit('setServerMessage', { message:error.response.data.message, heading:'Device Creation Failed!', active:true, type:'error' }, { root: true })
            console.log(error.response);
        })
    },
    setupDevice({ commit, dispatch, rootState}, data){
        axios.post(`/bartiny/setup/${data[1]}`, data[0], rootState.authentication.accessTokenConfig )
        .then(res => {
            if(res.status == 201){
                commit('setServerMessage', { message:res.data.message, heading:'Device Setup Completed', active:true, type:'success' }, { root: true })
                dispatch('getDevicesList')
            }
        })
        .catch(error => {
            commit('setServerMessage', { message:error.response.data.message, heading:'Device Setup Failed!', active:true, type:'error' }, { root: true })
            console.log(error.response);
        })
    },
    updateBartiny({ commit, dispatch, rootState}, data){
        console.log(data)
        axios.put(`/bartiny/update/${data[1]}`, data[0], rootState.authentication.accessTokenConfig )
        .then(res => {
            if(res.status == 200){
                commit('setServerMessage', { message:res.data.message, heading:'Bartiny Update Completed', active:true, type:'success' }, { root: true })
                dispatch('getBartinysList')
            }
        })
        .catch(error => {
            commit('setServerMessage', { message:error.response.data.message, heading:'Bartiny Update Failed!', active:true, type:'error' }, { root: true })
            console.log(error.response);
        })
    },
}

export default {
    state,
    mutations,
    actions,
    getters
}