<template>
  <div>
    <v-card class="mx-auto" width="500">
      <div style="padding:30px;width:100%">
        <div class="center">
          <img src="@/assets/logo-cap.svg" height="200px" />
        </div>
        <div class="center">
        <!-- <p class="title font-weight-regular" style="margin-top:0px">{{message}}</p> -->
        <v-alert outlined :type="type" v-if="message != null">{{ message }}</v-alert>
        </div>
        <v-row align="center">
          <v-col cols="6">
            <router-link class="link" small text to="/auth">Login? Click here</router-link>
          </v-col>
          <v-col class="text-right" cols="6">
            <router-link class="link" small text to="/auth/resend">Resend Confirmation</router-link>
          </v-col>
        </v-row>
      </div>
    </v-card>
  </div>
</template>

<script>
import axios from "axios";
  export default {
    name: 'Confirm',
    data() {
      return {
        message:null,
        type:null
      }
    },
    created(){
      axios.get(`/confirm/${this.$route.params.cid}`).then(res => {
      console.log(res);
      this.message = res.data.message
      this.type = 'success'

    })
    .catch(error => {
      console.log(error.response);
      this.message = error.response.data.message
      this.type = 'error'
    });
    },
  }
</script>

<style scoped>
  .center {
  text-align: center;
  }

  .link {
    text-transform: Capitalize;
    text-decoration: none;
    font-weight: 400;
    font-size: 12px;
  }

  h5 {
    font-weight: 400;
  }
</style>