<template>
    <v-container>
          <v-card class="mx-auto">
             <v-form ref="form" v-model="valid" lazy-validation>
            <div style="padding:30px;width:100%">
              <div class="center">
                <img src="@/assets/logo-cap.svg" height="200px" />
              </div>
              <div class="center">
                <p  class="title font-weight-reular">Register to access Aiila Products</p>
              </div>
              <v-row>
                <v-col>
                  <v-row no-gutters>
                    <v-col cols="12" md="6" lg="6" >
                    <v-text-field dense outlined label="First Name" prepend-inner-icon="person" v-model="form.first_name" :rules="firstNameRules" required></v-text-field>
                    </v-col>
                    <v-col cols="12" md="6" lg="6" class="gutter">
                    <v-text-field dense outlined label="Last Name" v-model="form.last_name" :rules="lastNameRules" required></v-text-field>
                    </v-col>
                  </v-row>
                  <v-text-field dense outlined label="Email" prepend-inner-icon="email" :rules="emailRules" required v-model="form.email"></v-text-field>
                  <v-text-field dense outlined label="Mobile Number" prepend-inner-icon="phone" :rules="mobileRules" v-model="form.mobile"></v-text-field>
                  <v-text-field dense outlined label="Password" prepend-inner-icon="lock" :append-icon="show ? 'mdi-eye-off' : 'mdi-eye'" :type="show ? 'text' : 'password'" @click:append="show = !show" :rules="passwordRules" required v-model="form.password"  ></v-text-field>
                </v-col>
              </v-row>
              
              <v-row align="center">
                  <v-col class="text-center" cols="12">
                      <v-btn color="primary" @click="validate">Submit</v-btn>
                  </v-col>
              </v-row>
              <!-- <v-row>
                <v-col>
                  <v-alert dismissible :type="authMessage.type" v-if="authMessage.msg != null">{{ authMessage.msg }}</v-alert>
                </v-col>
              </v-row> -->
              <v-row align="center" no-gutters="">
                  <v-col class="text-center"  cols="12">
                      <p style="font-size:12px; margin-bottom:0px">By clicking submit you agree to <router-link class="link" small text to="/auth/terms" style="text-transform:lowercase">terms of use</router-link></p>
                  </v-col>
              </v-row>

              <v-row align="center">
                  <v-col class="text-center" cols="12">
                      <router-link class="link" small text to="/auth">Already Registered? Login Here</router-link>
                  </v-col>
              </v-row>
              <v-row justify="center">
                <v-dialog v-model="authMessage.dialog" persistent max-width="350">
                  <v-card>
                    <v-card-title class="headline center" style="justify-content:center">{{ authMessage.heading }}</v-card-title>
                    <v-card-text class="center">{{ authMessage.msg }}</v-card-text>
                    <v-card-actions style="justify-content:center">
                      <v-btn text @click="closeDialog">close</v-btn>
                    </v-card-actions>
                  </v-card>
                </v-dialog>
              </v-row>
            
            </div>
             </v-form>
          </v-card>
    </v-container>
</template>

<script>
import { mapGetters } from 'vuex';
export default {
  name: "Register",
  data(){
      return {
          show: false,
          valid: true,
          form : { first_name:'Aiila', last_name:'Test Customer', email:'aiilainnovationsdevices@gmail.com', mobile:'9876543210', password:'Test@1234'},
          passwordRules: [ 
            v => !!v || 'Password is required', 
            v => (v && v.length >= 8) || 'Password must have 8+ characters',
            v => /(?=.*[A-Z])/.test(v) || 'Must have one uppercase character', 
            v => /(?=.*\d)/.test(v) || 'Must have one number', 
            v => /([!@$%])/.test(v) || 'Must have one special character [!@#$%]' 
          ],
          emailRules: [
            v => !!v || "E-mail is required",
            v => /.+@.+\..+/.test(v) || "E-mail must be valid"
          ],
          firstNameRules :[
            v => !!v || 'First name is required', 
            v => (v && v.length >= 5) || 'First Name must have 5+ characters',
          ],
          lastNameRules :[
            v => !!v || 'Last name is required', 
            v => (v && v.length >= 5) || 'Last Name must have 5+ characters',
          ],
          mobileRules: [
            v => !!v || 'Mobile Number is required',
            v => /^[a-zA-Z0-9\-().\s]{10,15}$/.test(v) || "Mobile number must be valid"
          ]
      }
  },
   methods: {
    validate() {
      if (this.$refs.form.validate()) {
        console.log(this.form);
        this.$store.dispatch("register", this.form);
      }
    },
    closeDialog(){
      if(this.authMessage.type=='success')
        this.$refs.form.reset();
      this.$store.dispatch("resetMessage");
    }
  },
  computed:{
    ...mapGetters(['authMessage']),
  },
};
</script>

<style scoped>
.center{
    text-align: center;
}

.link{
    text-transform: Capitalize;
    text-decoration: none;
    font-weight: 400;
    font-size: 12px;
}
@media (min-width: 960px)  {
  
  .gutter{
    padding-left: 10px;
  }
}

</style>
