<template>
  <div>
    <v-card class="mx-auto" width="500">
      <v-form ref="form" v-model="valid" lazy-validation>
        <div style="padding:30px;width:100%">
          <div class="center">
            <img src="@/assets/logo-cap.svg" height="200px" />
          </div>
          <div class="center">
            <p class="title font-weight-regular" style="margin-top:0px">Reset Password</p>
          </div>
          <div style="width:100%">
            <v-text-field
              outlined
              :rules="rules"
              v-model="form.email"
              label="Email"
              prepend-inner-icon="email"
            ></v-text-field>
          </div>
          <v-row align="center">
            <v-col class="text-center" cols="12">
              <v-btn @click="reset()" color="primary">Submit</v-btn>
            </v-col>
          </v-row>
          <v-row align="center" no-gutters>
            <v-col class="text-center" cols="12">
              <p
                style="font-size:12px; margin-bottom:0px"
              >By clicking submit you will get a reset link to email</p>
            </v-col>
          </v-row>

          <v-row align="center">
            <v-col class="text-center" cols="112">
              <router-link class="link" small text to="/auth">Already Registered? Login Here</router-link>
            </v-col>
          </v-row>
        </div>
      </v-form>
    </v-card>
    <v-dialog v-model="authMessage.dialog" persistent max-width="350">
      <v-card>
        <v-card-title class="title center" style="justify-content:center">{{ authMessage.heading }}</v-card-title>
        <v-card-text class="font-weight-regular center body-1">{{ authMessage.msg }}</v-card-text>
        <v-card-actions style="justify-content:center">
          <v-btn text @click="closeDialog">close</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "ResetRequest",
  data() {
    return {
      show: false,
      valid: true,
      dialog: false,
      rules: [value => !!value || "Registered email required."],
      form: {
        email: ""
      }
    };
  },
  methods: {
    reset() {
      if (this.$refs.form.validate()) {
        this.$store.dispatch("resetRequest", {
          email: this.form.email
        });
      }
    },
    closeDialog() {
      if (this.authMessage.type == "success") this.$refs.form.reset();
      this.$store.dispatch("resetMessage");
    },
   
  },
   computed: {
      ...mapGetters(["authMessage"])
    }
};
</script>

<style scoped>
.center {
  text-align: center;
}

.link {
  text-transform: Capitalize;
  text-decoration: none;
  font-weight: 400;
  font-size: 12px;
}
</style>
