<template>
  <div class="ing_types" style="width:inherit">
    <div class="heading d-flex">
      <div>
        <v-card
          color="accent"
          height="65px"
          width="65px"
          class="d-flex justify-center align-center" >
          <img height="35px" width="35px" src="@/assets/icons/ingredients-white.svg" />
        </v-card>
      </div>
      <div style="width:100%;margin-left:10px">
        <v-card height="65px">
          <v-card-title>Requisite Groups</v-card-title>
        </v-card>
      </div>
    </div>
    <div class="content">
      <v-card class="mt-3 pa-3 pb-0">
        <v-row class="pb-0">
          <v-col class="pb-0">
            <v-text-field dense label="Search"  v-model="search" single-line outlined append-icon="search"></v-text-field>
          </v-col>
          <v-col class="pb-0">
          </v-col>
          <v-col class="pb-0">
          </v-col>
          <v-col class="pb-0">
          </v-col>
          <v-col class="pb-0">
          </v-col>
        </v-row>
      </v-card>
      <v-card style="margin-top:10px">
        <v-card-title>
          <v-spacer></v-spacer>
          <v-btn depressed color="primary" @click="addNewRequisiteGroupDialog()">Add New</v-btn>
        </v-card-title>
        <v-data-table :headers="headers" :search="search" :items="getRequisiteGroups" :items-per-page="10">
          <template v-slot:item.image="{ item }">
            <div class="p-2">
              <v-img :src="item.img_uri" :alt="item.name" height="40px" width="40px" style="border-radius:20px;"></v-img>
            </div>
          </template>
        </v-data-table>
      </v-card>
    
    </div>
    <div>
       <v-dialog v-model="new_dialog" persistent max-width="400px">
          <v-card>
            <v-container>
              <v-card-title>
                <span class="headline">New Requisite Group</span>
              </v-card-title>
              <v-card-text>
                <v-form ref="new_form" v-model="valid" lazy-validation>
                  <v-row no-gutters>
                    <v-col cols="12">
                      <v-text-field
                        outlined
                        dense
                        label="Requisite Group Name*"
                        :rules="new_rules.group_name"
                        v-model="new_form.group_name"
                        required
                      ></v-text-field>
                    </v-col>
                    <v-col cols="12">
                      <v-text-field
                        outlined
                        dense
                        label="Dispensing Factor*"
                        :rules="new_rules.disp_factor"
                        v-model="new_form.disp_factor"
                        required
                      ></v-text-field>
                    </v-col>
                    <v-col cols="12">
                      <v-select
                        outlined
                        :items="units"
                        dense
                        label="Measuring Units"
                        :rules="new_rules.measuring_units"
                        v-model="new_form.measuring_units"
                        required
                      ></v-select>
                    </v-col>
                     <v-col cols="12">
                      <v-select
                        outlined
                        :items="disp"
                        dense
                        label="Dispensable"
                        :rules="new_rules.dispensable"
                        v-model="new_form.dispensable"
                        required
                      ></v-select>
                    </v-col>
                  </v-row>
                </v-form>
                <small>*indicates required field</small>
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="secondary" @click="new_dialog = false">Cancel</v-btn>
                <v-btn color="primary" @click="newRequisiteGroup()">Add</v-btn>
              </v-card-actions>
            </v-container>
          </v-card>
        </v-dialog>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "RequisiteTypes",
  data() {
    return {
      show: false,
      search:'',
      list: true,
      headers: [
        { text: "Requisite Group", value: "group_name" },
        { text: "Measuring Units", value: "units" },
        { text: "Machine Dispensable", value: "dispensable" },
        { text: "Dispensing Factor", value: "disp_factor" },
      ],
      item:'',
      itemsPerPage: 12,
      page:1,
      new_form: {
        group_name: "",
        dispensable: "",
        measuring_units: "",
        disp_factor:""
      },
      new_rules: {
        group_name: [value => !!value || "Requisite Group name required."],
        dispensable: [value => !!value || "Is it dispensable"],
        measuring_units: [  v => !!v || "Measuring Units required",  ],
        disp_factor: [  v => !!v || "Dispensing Factor required",  ]
      },
      valid: true,
      new_dialog: false,
      units:['ml','no'],
      disp:['Yes','No']
    };
  },
   created () {
     if (this.getRequisiteGroups.length == 0) {
      this.$store.dispatch('getRequisiteGroupsList')
    }
  },
  computed: {
    ...mapGetters([
      "getRequisiteGroups",
      "getServerMessage"
    ]),
  },
  methods:{
    toggleView() {
      this.list = ! this.list;
    },
    newRequisiteGroup() {
      if (this.$refs.new_form.validate()) {
        this.$store.dispatch("setRequisiteGroup", this.new_form);
      }
    },
    addNewRequisiteGroupDialog() {
      this.new_dialog = true;
    }
  },
  watch:{
     getServerMessage: function() {
      if (this.new_dialog) {
        if (
          this.getServerMessage.active &&
          this.getServerMessage.type === "success"
        ) {
          this.new_dialog = false;
          this.$refs.new_form.reset();
        }
      }
    }
  }
};
</script>
