import axios from "axios";

const state = {
    susers: [],
}

const getters = {
    getSuperUsers(state) {
        return state.susers
    }
}

const mutations = {
    setSuperUsers(state, data){
        state.susers = data
    },
}

const actions = {
    getSuperUsersList({commit, rootState}){
        axios.get("/users?user_type=1", rootState.authentication.accessTokenConfig)
        .then(res => {
            commit('setSuperUsers', res.data)
        })
        .catch(error => {
            console.log(error.response);
        })
    },
    setSuperUser({ commit, dispatch, rootState }, data) {
  
        axios.post("/user/create", data, rootState.authentication.accessFileTokenConfig )
        .then(res => {
            if(res.status == 201){
                commit('setServerMessage', { message:res.data.message, heading:'New Super User Created', active:true, type:'success' }, { root: true })
                dispatch('getSuperUsersList')
            }
        })
        .catch(error => {
            commit('setServerMessage', { message:error.response.data.message, heading:'New Super User created failed!', active:true, type:'error' }, { root: true })
            console.log(error.response);
        })
      },
}

export default {
    state,
    mutations,
    actions,
    getters
}