import axios from "axios";

const state = {
    users: [],
}

const getters = {
    getUsers(state) {
        return state.users
    }
}

const mutations = {
    setUsers(state, data){
        state.users = data
    },
}

const actions = {
    getUsersList({commit, rootState}){
        axios.get("/users?user_type=0", rootState.authentication.accessTokenConfig)
        .then(res => {
            commit('setUsers', res.data)
        })
        .catch(error => {
            console.log(error.response);
        })
    },
}

export default {
    state,
    mutations,
    actions,
    getters
}