<template>
  <div class="ing_types" style="width:inherit">
    <div class="heading d-flex">
      <div>
        <v-card
          color="accent"
          height="65px"
          width="65px"
          class="d-flex justify-center align-center" >
          <img height="35px" width="35px" src="@/assets/icons/ingredients-white.svg" />
        </v-card>
      </div>
      <div style="width:100%;margin-left:10px">
        <v-card height="65px">
          <v-card-title>Requisite Types</v-card-title>
        </v-card>
      </div>
      <div style="margin-left:10px">
        <v-card
          color="accent"
          height="65px"
          width="65px"
          class="d-flex justify-center align-center"  @click="toggleView" >
          <v-icon color="white">{{ list? 'format_list_bulleted':'apps' }}</v-icon>
        </v-card>
      </div>
    </div>
    <div class="content">
      <v-card class="mt-3 pa-3 pb-0">
        <v-row class="pb-0">
          <v-col class="pb-0">
            <v-text-field dense label="Search"  v-model="search" single-line outlined append-icon="search"></v-text-field>
          </v-col>
          <v-col class="pb-0">
            <v-select dense :items="generic_type" item-text="name" item-value="id" label="Select Requisite Group" outlined></v-select>
          </v-col>
          <v-col class="pb-0">
          </v-col>
          <v-col class="pb-0">
          </v-col>
          <v-col class="pb-0">
          </v-col>
        </v-row>
      </v-card>
      <v-card v-if="list" style="margin-top:10px">
        <v-card-title>
          <v-spacer></v-spacer>
          <v-btn depressed color="primary" @click="addNewRequisiteTypeDialog()">Add New</v-btn>
        </v-card-title>
        <v-data-table :headers="headers" :search="search" :items="getRequisiteTypes" :items-per-page="10">
          <template v-slot:item.image="{ item }">
            <div class="p-2">
              <v-img :src="item.img_uri" :alt="item.name" height="40px" width="40px" style="border-radius:20px;"></v-img>
            </div>
          </template>
        </v-data-table>
      </v-card>
      <div v-else>
         <v-data-iterator :items="getRequisiteTypes" :items-per-page.sync="itemsPerPage" :page="page" :search="search" hide-default-footer>
          <template v-slot:default="props">
            <v-row>
              <v-col v-for="item in props.items" :key="item.name" cols="12" sm="6" md="2" lg="2">
                <v-card>
                  <v-card>
                    <v-img :src="item.img_uri" height="250px" style="background:rgb(0,0,0,.1)"></v-img>
                    <v-card-title class="subheading font-weight-bold">{{ item.type_name }}</v-card-title>
                    <v-card-subtitle>{{item.group_name}}</v-card-subtitle>
                    <v-list dense>

                      <v-list-item>
                        <v-list-item-content>Dispensable:</v-list-item-content>
                        <v-list-item-content class="align-end">{{ item.dispensable }}</v-list-item-content>
                      </v-list-item>

                    </v-list>
                    <v-divider></v-divider>
                    <v-card-actions>
                      <v-spacer></v-spacer>
                      <v-btn text>Edit</v-btn>
                    </v-card-actions>
                  </v-card>
                </v-card>
              </v-col>
            </v-row>
          </template>
        </v-data-iterator>
        <template>
          <div class="text-center">
            <v-pagination
              v-model="page"
              :length="numberOfPages"
              prev-icon="mdi-menu-left"
              next-icon="mdi-menu-right"
              @next="next()"
            ></v-pagination>
          </div>
        </template>
      </div>
    </div>
    <div>
       <v-dialog v-model="new_dialog" persistent max-width="400px">
          <v-card>
            <v-container>
              <v-card-title>
                <span class="headline">New Requisite Type</span>
              </v-card-title>
              <v-card-text>
                <v-form ref="new_form" v-model="valid" lazy-validation>
                  <v-row no-gutters>
                    <v-col>
                      <v-card
                        id="preview"
                        height="200px"
                        width="200px"
                        class="mx-auto mt-2 mb-6 pa-4"
                        outlined
                        @click.native="openFileDialog"
                      >
                        <v-img width="100%" height="100%" :src="getImgUrl(url)" />
                      </v-card>
                      <div v-if="image_error" class="v-messages__message mb-3" style="text-align:center; color:red">Image required.</div>
                    </v-col>
                    <v-col cols="12">
                      <v-file-input
                        style="display:none"
                        id="file"
                        required
                        accept="image/png, image/jpeg, image/bmp"
                        placeholder="Pick an image"
                        prepend-icon="mdi-camera"
                        label="Requisite Image"
                        outlined
                        :rules="new_rules.image"
                        @change="onFileChange"
                      ></v-file-input>
                    </v-col>
                    <v-col cols="12">
                      <v-text-field
                        outlined
                        dense
                        label="Requisite Type Name*"
                        :rules="new_rules.type_name"
                        v-model="new_form.type_name"
                        required
                      ></v-text-field>
                    </v-col>
                    <v-col cols="12">
                      <v-select
                        outlined
                        :items="getRequisiteGroups"
                        item-text="name"
                        item-value="id"
                        dense
                        label="Requisite Group*"
                        :rules="new_rules.group_id"
                        v-model="new_form.group_id"
                        required
                      ></v-select>
                    </v-col>
                  </v-row>
                </v-form>
                <small>*indicates required field</small>
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="secondary" @click="new_dialog = false">Cancel</v-btn>
                <v-btn color="primary" @click="newRequisiteType()">Add</v-btn>
              </v-card-actions>
            </v-container>
          </v-card>
        </v-dialog>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "RequisiteTypes",
  data() {
    return {
      show: false,
      search:'',
      list: true,
      headers: [
        { text: "Image", value: "image", sortable: false},
        { text: "Requisite Type", value: "type_name" },
        { text: "Requisite Group", value: "group_name" },
        { text: "Machine Dispensable", value: "dispensable" },
      ],
      item:'',
      itemsPerPage: 12,
      page:1,
      new_form: {
        type_name: "",
        group_id: "",
        image: ""
      },
      new_rules: {
        type_name: [value => !!value || "Requisite Type name required."],
        group_id: [value => !!value || "Requisite Group required."],
        image: [
          v => !!v || "Image is required",
          value =>
            !value || value.size < 1000000 || "Image should be less than 2 MB!"
        ]
      },
      valid: true,
      image_error:false,
      new_dialog: false,
      url:null,
      file:null,
    };
  },
   created () {
    if (this.getRequisiteTypes.length == 0) {
      this.$store.dispatch('getRequisitesTypesList')
    }
     if (this.getRequisiteGroups.length == 0) {
      this.$store.dispatch('getRequisiteGroupsList')
    }
  },
  computed: {
    ...mapGetters([
      "getRequisiteGroups",
      "getRequisiteTypes",
      "getServerMessage"
    ]),
    generic_type() {
      const SelectValue = ["All"];
      return SelectValue.concat(this.getRequisiteGroups);
    },
    ing_types() {
      const SelectValue = ["All"];
      return SelectValue.concat(this.getRequisiteTypes);
    },
    numberOfPages() {
      return Math.ceil(this.getRequisiteTypes.length / this.itemsPerPage);
    }
  },
  methods:{
    toggleView() {
      this.list = ! this.list;
    },
     nextPage() {
      if (this.page + 1 <= this.numberOfPages) this.page += 1;
    },
    formerPage() {
      if (this.page - 1 >= 1) this.page -= 1;
    },
    updateItemsPerPage(number) {
      this.itemsPerPage = number;
    },
    newRequisiteType() {
      if (this.$refs.new_form.validate()) {
        let formData = new FormData();
        formData.append('image',this.file)
        formData.append('ing_type_name',this.new_form.ing_type_name)
        formData.append('gen_type_id',this.new_form.gen_type_id)
        this.$store.dispatch("setRequisiteType", formData);
      } else {
        if(!this.file){
          this.image_error=true
        }
      }
    },
    addNewRequisiteTypeDialog() {
      this.new_dialog = true;
    },
    openFileDialog() {
      document.getElementById("file").click();
    },
    onFileChange(e) {
      this.file = e;
      this.url = URL.createObjectURL(e);
      console.log(e)
    },
    getImgUrl(url) {
      if (url == null) {
        return require("@/assets/image_placeholder.png");
      } else {
        return url;
      }
    },
  },
  watch:{
     getServerMessage: function() {
      if (this.new_dialog) {
        if (
          this.getServerMessage.active &&
          this.getServerMessage.type === "success"
        ) {
          this.new_dialog = false;
          this.$refs.new_form.reset();
        }
      }
    }
  }
};
</script>
