<template>
  <div class="req_types" style="width:inherit">
    <div class="heading d-flex">
      <div>
        <v-card
          color="accent"
          height="65px"
          width="65px"
          class="d-flex justify-center align-center"
        >
          <img height="35px" width="35px" src="@/assets/icons/ingredients-white.svg" />
        </v-card>
      </div>
      <div style="width:100%;margin-left:10px">
        <v-card height="65px">
          <v-card-title>Cocktails Library</v-card-title>
        </v-card>
      </div>
      <div style="margin-left:10px">
        <v-card
          color="accent"
          height="65px"
          width="65px"
          class="d-flex justify-center align-center"
          @click="toggleView"
        >
          <v-icon color="white">{{ list? 'format_list_bulleted':'apps' }}</v-icon>
        </v-card>
      </div>
    </div>
    <div class="content">
      <v-card class="mt-3 pa-3 pb-0">
        <v-row class="pb-0">
          <v-col class="pb-0">
            <v-text-field
              label="Search Cocktails"
              v-model="search"
              dense
              outlined
              append-icon="search"
            ></v-text-field>
          </v-col>

          <v-col class="pb-0">
            <v-select
              :items="cocktailTypes"
              dense
              item-text="type_name"
              item-value="id"
              label="Select Cocktails Type"
              outlined
            ></v-select>
          </v-col>
          <v-col class="pb-0"></v-col>
          <v-col class="pb-0"></v-col>
          <v-col class="pb-0"></v-col>
        </v-row>
      </v-card>
      <v-card v-if="list" class="mt-3">
        <v-card-title>
          Products
          <v-spacer></v-spacer>
          <v-btn depressed color="primary" @click="addNew()">Add New</v-btn>
        </v-card-title>
        <v-data-table
          :search="search"
          :headers="headers"
          :items="cocktails"
          :items-per-page="10"
          :single-expand="singleExpand"
          :expanded.sync="expanded"
          item-key="cocktail_name"
          show-expand
        >
          <template v-slot:item.image="{ item }">
            <div class="p-2">
              <v-img
                :src="item.img_uri"
                :alt="item.req_name"
                height="40px"
                width="40px"
                style="border-radius:20px;"
              ></v-img>
            </div>
          </template>
          <template v-slot:expanded-item="{ headers, item }">
            <td :colspan="headers.length" style="padding:0px">
              <v-card class="ma-4">
                <v-simple-table>
                  <template v-slot:default>
                    <thead>
                      <tr>
                        <th colspan="8" class="text-center">Requisites</th>
                      </tr>
                      <tr>
                        <th>Name</th>
                        <th>Quantity</th>
                        <th>Type</th>
                        <th>Group</th>
                        <th>Dispensable</th>
                        <th>Dispensing factor</th>
                        <th>Priority</th>
                        <th>Description</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="(i, index) in item.req_list" :key="index">
                        <td>{{ i.req_name }}</td>
                        <td>{{ i.req_quantity }}{{i.req_units}}</td>
                        <td>{{ i.req_type_name }}</td>
                        <td>{{ i.req_group_name }}</td>
                        <td>{{ i.dispensable }}</td>
                        <td>{{ i.disp_factor }}</td>
                        <td>{{ i.req_priority }}</td>
                        <td>{{ i.req_desc }}</td>
                      </tr>
                    </tbody>
                  </template>
                </v-simple-table>
              </v-card>
            </td>
          </template>
        </v-data-table>
      </v-card>
      <div v-else>
        <v-row>
          <v-col v-for="item in cocktails" :key="item.name" cols="12" sm="6" md="3" lg="3">
            <v-card>
              <v-img :src="item.img_uri" height="250px" style="background:rgb(0,0,0,.1)"></v-img>
              <v-card-title class="subheading font-weight-bold">{{ item.ct_name }}</v-card-title>
              <v-card-subtitle>{{item.cocktail_type_name}}</v-card-subtitle>
              <v-list dense>
                <v-list-item>
                  <v-list-item-content>Description:</v-list-item-content>
                  <v-list-item-content class="align-end">{{ item.ct_desc }}</v-list-item-content>
                </v-list-item>

                <v-list-item>
                  <v-list-item-content>Procedure:</v-list-item-content>
                  <v-list-item-content class="align-end">{{ item.ct_procedure }}</v-list-item-content>
                </v-list-item>

                <v-list-item>
                  <v-list-item-content>Requisites:</v-list-item-content>
                </v-list-item>
                <v-list-item>
                  <v-list-item-content>
                    <p v-for="(ing,index) in item.req_list" :key="ing.id">
                      <span>{{index+1}}. {{ing.req_name}}({{ing.req_type_name}}) - {{ing.req_quantity}}{{ing.req_units}}</span>
                    </p>
                  </v-list-item-content>
                </v-list-item>
              </v-list>
              <v-divider></v-divider>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn text>Edit</v-btn>
              </v-card-actions>
            </v-card>
          </v-col>
        </v-row>
      </div>
    </div>
    <div>
      <v-dialog v-model="dialog" persistent max-width="1100px">
        <v-card>
          <v-container>
            <v-card-title>
              <span class="headline">Create New Cocktail</span>
            </v-card-title>
            <v-card-text>
              <v-form ref="form" v-model="valid" lazy-validation>
                <v-row no-gutters>
                  <v-col cols="12">
                    <v-row>
                      <v-col cols="12" md="5">
                        <v-row no-gutters>
                          <v-col>
                            <v-card
                              id="preview"
                              height="200px"
                              width="200px"
                              class="mx-auto mb-6 pa-4 pt-0"
                              outlined
                              @click.native="openFileDialog"
                            >
                              <v-img width="100%" height="100%" :src="getImgUrl(url)" />
                            </v-card>
                            <div
                              v-if="image_error"
                              class="v-messages__message mb-3"
                              style="text-align:center; color:red"
                            >Image required.</div>
                          </v-col>
                          <v-col cols="12">
                            <v-file-input
                              style="display:none"
                              id="file"
                              required
                              accept="image/png, image/jpeg, image/bmp"
                              placeholder="Pick an image"
                              prepend-icon="mdi-camera"
                              label="Requisite Image"
                              :rules="rules.img_uri"
                              outlined
                              @change="onFileChange"
                            ></v-file-input>
                          </v-col>
                        </v-row>
                      </v-col>
                      <v-col cols="12" md="7">
                        <v-row no-gutters>
                          <v-col cols="12">
                            <v-text-field
                              outlined
                              dense
                              label="Cocktail Name*"
                              v-model="form.ct_name"
                              :rules="rules.ct_name"
                              required
                            ></v-text-field>
                          </v-col>
                          <v-col cols="12">
                            <v-select
                              outlined
                              dense
                              :items="getCocktailTypes"
                              label="Cocktail Type"
                              item-text="type_name"
                              item-value="id"
                              @change="updateList()"
                              v-model="form.ct_type_id"
                              :rules="rules.ct_type_id"
                              required
                            ></v-select>
                          </v-col>
                          <v-col cols="12">
                            <v-text-field :rules="rules.ct_procedure" v-model="form.ct_procedure" outlined dense label="Preperation*" required></v-text-field>
                          </v-col>
                        </v-row>
                      </v-col>
                    </v-row>
                  </v-col>
                  <v-col cols="12">
                    <p class="subtitle-1">Requisites List</p>
                  </v-col>
                  <v-col cols="12" v-for="(i, index) in form.req_list" :key="index">
                    <v-row no-gutters>
                       <v-col class="pr-sm-3"  cols="13" sm="6" md="3">
                            <v-select
                              outlined
                              dense
                              :items="req_types"
                              label="Requisite Type"
                              item-text="type_name"
                              item-value="id"
                              v-model="i.req_type_id"
                              :rules="rules.req_list.req_type_id"
                              required
                            ></v-select>
                          </v-col>
                      <v-col class="pr-sm-3" cols="12" sm="6" md="2">
                        <v-autocomplete outlined dense  :items="requisites"  label="Select Requisite" item-text="name" item-value="id" v-model="i.req_id" :rules="rules.req_list.req_id" required ></v-autocomplete>
                      </v-col>
                      <v-col class="pr-sm-3" cols="12" sm="6" md="2">
                        <v-text-field type="number" :rules="rules.req_list.req_quantity" v-model="i.req_quantity" outlined dense label="Quantity*" required></v-text-field>
                      </v-col>
                      <v-col class="pr-sm-3" cols="12" sm="6" md="2">
                        <v-text-field type="number" :rules="rules.req_list.req_priority" v-model="i.req_priority" outlined dense label="priority*" required></v-text-field>
                      </v-col>
                      <v-col class="pr-sm-3" cols="12" sm="6" md="2">
                        <v-text-field outlined dense label="Step Description*" v-model="i.req_desc" required></v-text-field>
                      </v-col>
                      <v-col cols="12" sm="6" md="1" class="d-flex justify-end">
                        <v-btn @click="removeItem(index)" icon>
                          <v-icon>mdi-delete</v-icon>
                        </v-btn>
                      </v-col>
                    </v-row>
                  </v-col>
                  <v-col cols="12">
                    <v-row no-gutters>
                      <v-col cols="12" class="d-flex justify-end">
                        <v-btn @click="addItem()" icon>
                          <v-icon>mdi-plus-circle</v-icon>
                        </v-btn>
                      </v-col>
                    </v-row>
                  </v-col>
                  <v-col cols="12">
                    <v-textarea outlined dense v-model="form.ct_desc" label="Description*" required></v-textarea>
                  </v-col>
                </v-row>
              </v-form>

              <small>*indicates required field</small>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="secondary" @click="dialog = false">Cancel</v-btn>
              <v-btn color="primary" @click="newItem()">Create</v-btn>
            </v-card-actions>
          </v-container>
        </v-card>
      </v-dialog>
      <v-dialog v-model="alertDialog" persistent max-width="350">
        <v-card>
          <v-card-title
            class="center"
            style="justify-content:center"
          >Form Reset Alert</v-card-title>
          <v-card-text color="red" class="text-center body-1">If you change this option the existing requisite list will reset.</v-card-text>
          <v-card-actions style="justify-content:center">
            <v-btn text @click="closeAlert()">Cancel</v-btn>
            <v-btn text @click="updateList()">Proceed</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "Cocktails",
  data() {
    return {
      show: false,
      expanded: [],
      singleExpand: false,
      search: "",
      list: true,
      dialog: false,
      valid: true,
      url: null,
      file: null,
      image_error: false,
      priority_count: 1,
      alertDialog:false,
      headers: [
        { text: "Image", value: "image", sortable: false },
        { text: "Cocktail Name", value: "ct_name", sortable: false },
        { text: "Cocktail Type", value: "ct_type_name" },
        { text: "Description", value: "ct_desc" },
        { text: "Procedure", value: "ct_procedure" },
        { text: "", value: "data-table-expand", sortable: false }
      ],
      
      form: {
        ct_name: "",
        ct_type_id: null,
        ct_procedure: "",
        ct_desc: "",
        img_uri: '',
        req_list: [
        ]
      },
      rules: {
        ct_name: [value => !!value || "Cocktail name required."],
        ct_type_id: [value => !!value || "Cocktail Type required."],
        ct_procedure: [value => !!value || "Cocktail preperation required."],
        ct_desc: [value => !!value || "Cocktail description required."],
        img_uri:  [value => !!value || "Cocktail image required."],
        req_list:{
          req_type_id:[value => !!value || "Requisite type required."],
          req_id:[value => !!value || (value == 0) || "Requisite required."],
          req_quantity: [value => !!value || "Requisite quantity required."],
          req_priority: [value => !!value || "Requisite priority required."]
        }
      },
      temp_var:null
    };
  },
  methods: {
    toggleView() {
      this.list = !this.list;
    },
    addNew() {
      this.dialog = true;
    },
    openFileDialog() {
      document.getElementById("file").click();
    },
    onFileChange(e) {
      this.file = e;
      this.url = URL.createObjectURL(e);
      console.log(e);
    },
    getImgUrl(url) {
      if (url == null) {
        return require("@/assets/image_placeholder.png");
      } else {
        return url;
      }
    },
    // priorityCounter(){
    //     if(this.form.ct_type_id == 1){
    //       return this.priority_count++;
    //     }
    //      if(this.form.ct_type_id == 2){
    //       return this.priority_count;
    //     }
    // },
    removeItem(index) {
      // this.priority_count--
      if (index > -1 && this.form.req_list[index].req_type_id!=13) {
        this.form.req_list.splice(index, 1);
      }
    },
    addItem() {
      this.form.req_list.push({
        req_type_id:null,
        req_id: null,
        req_quantity: null,
        req_priority: null,
        req_desc: ""
      });
    },
     newItem() {
      if (this.$refs.form.validate()) {
        let image = {
          name: this.form.ct_name + this.form.ct_type_id,
          file:this.file
        }
        this.form.img_uri = this.form.ct_name.toLowerCase().replace(/'('|')'|' '/g,"_") +"_" +this.form.ct_type_id
        this.$store.dispatch("setCocktail", { cocktail:this.form, image:image });
      } else {
        if(!this.file){
          this.image_error=true
        }
      }
    },
    updateList(){
     
      this.form.req_list = [
        { req_type_id:13, req_id: null, req_quantity: 1, req_priority: 1, req_desc: "" },
      ]
      this.filterCtType(this.form.ct_type_id).actions.forEach(element => {
        let stype = this.requisites.filter( test => {
         if(test.id == element.req_id)
         return test
        })
        this.form.req_list.push({
          req_type_id:stype[0].req_type_id, req_id: element.req_id, req_quantity: null, req_priority: 1, req_desc: ""
        })
      });
       this.form.req_list.push({
          req_type_id:null, req_id: null, req_quantity: null, req_priority: 1, req_desc: ""
        })
    },

    filterCtType(id){
       let ctype =  this.getCocktailTypes.filter(type=> {
        if(type.id == id) {
          return type
        }
      })
      return ctype[0]
    }

  },
  computed: {
    ...mapGetters([
      "getCocktails",
      "getCocktailTypes",
      "getRequisites",
      "getRequisiteTypes",
      "getServerMessage",
    ]),
    cocktails() {
      return this.getCocktails;
    },
    cocktailTypes() {
      const SelectValue = ["All"];
      return SelectValue.concat(this.getCocktailTypes);
    },
    requisites() {
      const SelectValue = [{
        id: 0,
        name: "Any"
      }];
      return SelectValue.concat(this.getRequisites);
    },
    req_types() {
      return this.getRequisiteTypes
    },
  },
  created() {
    if (this.getRequisites.length == 0) {
      this.$store.dispatch("getRequisitesList");
    }
    if (this.getCocktails.length == 0) {
      this.$store.dispatch("getCocktailsList");
      console.log(this.getCocktails);
    }
    if (this.getCocktailTypes.length == 0) {
      this.$store.dispatch("getCocktailTypesList");
      console.log(this.getRequisiteTypes);
    }
    if (this.getRequisiteTypes.length == 0) {
      this.$store.dispatch("getRequisitesTypesList");
    }
  },
  watch: {
    getServerMessage: function() {
      if (this.dialog) {
        if (
          this.getServerMessage.active &&
          this.getServerMessage.type === "success"
        ) {
          this.dialog = false;
          this.$refs.form.reset();
        }
      }
    }
  }
};
</script>
