import Vue from 'vue';
import Vuex from 'vuex';

import devices from './modules/devices';
import requisites from './modules/requisites';
import requisiteTypes from './modules/requisiteTypes';
import cocktails from './modules/cocktails';
import superusers from './modules/superusers';
import users from './modules/users';
import overview from './modules/overview';
import requisiteGroups from './modules/requisiteGroups';
import authentication from './modules/authentication';
import cocktailtypes from './modules/cocktailtypes';
import ServerMessage from './modules/ServerMessage';

Vue.use(Vuex);

export default new Vuex.Store({
    modules: {
        devices,
        requisites,
        requisiteTypes,
        cocktails,
        cocktailtypes,
        superusers,
        users,
        overview,
        requisiteGroups,
        authentication,
        ServerMessage
    },
})