const state = {
    message:null,
    heading:null,
    active:false,
    type:null
}

const getters = {
    getServerMessage(state){
        return { message: state.message, heading: state.heading, active:state.active, type:state.type }
    }
}

const mutations = {
    setServerMessage(state,data){
        state.message = data.message;
        state.heading = data.heading,
        state.active = data.active,
        state.type = data.type
    },

    clearServerMessage(state){
        state.message = null;
        state.heading = null,
        state.active = false,
        state.type = null
    }
}

const actions = {
    clearMessage({ commit }){
        commit("clearServerMessage");
      },
}


export default {
    state,
    mutations,
    actions,
    getters
}