<template>
  <div class="Devices" style="width:inherit">
    <div class="heading d-flex">
      <div>
        <v-card
          color="accent"
          height="65px"
          width="65px"
          class="d-flex justify-center align-center"
        >
          <img height="35px" width="35px" src="@/assets/icons/device-white.svg" />
        </v-card>
      </div>
      <div style="width:100%;margin-left:10px">
        <v-card height="65px">
          <v-card-title>Bartiny</v-card-title>
        </v-card>
      </div>
    </div>
    <div class="content">
      <v-card class="mt-3 pa-3 pb-0">
        <v-row class="pb-0">
          <v-col class="pb-0">
            <v-text-field dense label="Search Serial No" single-line outlined append-icon="search"></v-text-field>
          </v-col>
           <v-col class="pb-0">
            <!-- <v-select dense :items="products" item-value="code" item-text="name" label="Select Product" outlined></v-select> -->
          </v-col>
          <v-col class="pb-0">
            <v-select dense :items="productModels" item-text="model_no" item-value="model_no" label="Select Model" outlined></v-select>
          </v-col >
          <v-col class="pb-0">
            <v-menu ref="begin_menu" v-model="begin_menu" :close-on-content-click="false" :return-value.sync="begin_date"  transition="scale-transition" offset-y min-width="290px"  >
              <template v-slot:activator="{ on }">
                <v-text-field
                  outlined dense
                  v-model="begin_date"
                  label="Begin Date"
                  prepend-inner-icon="event"
                  v-on="on"
                ></v-text-field>
              </template>
              <v-date-picker v-model="begin_date" no-title scrollable>
                <v-spacer></v-spacer>
                <v-btn text color="primary" @click="begin_menu = false">Cancel</v-btn>
                <v-btn text color="primary" @click="$refs.begin_menu.save(begin_date)">OK</v-btn>
              </v-date-picker>
            </v-menu>
          </v-col>
          <v-col class="pb-0">
             <v-menu ref="end_menu" v-model="end_menu" :close-on-content-click="false" :return-value.sync="end_date"  transition="scale-transition" offset-y min-width="290px"  >
              <template v-slot:activator="{ on }">
                <v-text-field
                  outlined dense
                  v-model="end_date"
                  label="End Date"
                  prepend-inner-icon="event"
                  v-on="on"
                ></v-text-field>
              </template>
              <v-date-picker v-model="end_date" no-title scrollable>
                <v-spacer></v-spacer>
                <v-btn text color="primary" @click="end_menu = false">Cancel</v-btn>
                <v-btn text color="primary" @click="$refs.end_menu.save(end_date)">OK</v-btn>
              </v-date-picker>
            </v-menu>
          </v-col>
        </v-row>
      </v-card>
      <v-card style="margin-top:10px">
        <v-data-table :headers="headers" :items="bartinys" :items-per-page="10"  
        :single-expand="singleExpand"
          :expanded.sync="expanded" 
          show-expand
          item-key="serial_no" >
           <template v-slot:item.actions="{ item }">
            <v-menu bottom origin="center center" transition="scale-transition">
              <template v-slot:activator="{ on }">
                <v-btn icon v-on="on">
                  <v-icon>mdi-dots-vertical</v-icon>
                </v-btn>
              </template>

              <v-list dense>
                <v-list-item-group color="primary">
                  <v-list-item @click="updateBartinyDialog(item)">
                    <v-list-item-icon>
                      <v-icon>update</v-icon>
                    </v-list-item-icon>
                    <v-list-item-content>
                      <v-list-item-title>Update</v-list-item-title>
                    </v-list-item-content>
                  </v-list-item>
                </v-list-item-group>
              </v-list>
            </v-menu>
          </template>
          <template v-slot:expanded-item="{ headers, item }">
            <td :colspan="headers.length" style="padding:0px">
              <v-simple-table style="margin: 12px">
                <template v-slot:default>
                  <thead>
                    <tr>
                      <th colspan="4" class="text-center">Technical Details</th>
                    </tr>
                  </thead>
                  <tbody>
                     <tr>
                      <td style="width:250px">Hardware Verison</td> <td >{{ item.hardware_revision }}</td> <td style="width:250px">Motherboard Serial Number</td> <td >{{ item.motherboard_no }}</td>
                    </tr>
                     <tr>
                      <td style="width:250px">Product Revision</td> <td >{{ item.product_revision }}</td><td style="width:250px">IMEI Number</td> <td >{{ item.imei_code }}</td>
                    </tr> 
                    <tr>
                      <td style="width:250px">API Version</td> <td >{{ item.api_version }}</td><td style="width:250px">Bluetooth MAC Address</td> <td >{{ item.bluetooth }}</td>
                    </tr>
                     <tr>
                      <td style="width:250px">AD Serial Number</td> <td >{{ item.chassis_no }}</td> <td style="width:250px">INIT Code</td> <td >{{ item.init_code }}</td>
                    </tr>
                     <tr >
                      <td style="width:250px"></td> <td>Installed Versions</td><td colspan="2">Available Versions</td>
                    </tr>
                     <tr >
                      <td style="width:250px">Firmware</td> <td>{{ item.installed_versions.firmware }}</td><td colspan="2">{{ item.available_versions.firmware }}</td>
                    </tr>
                     <tr>
                      <td style="width:250px">Software</td> <td>{{ item.installed_versions.software }}</td><td colspan="2">{{ item.installed_versions.firmware }}</td>
                    </tr>
                    
                  </tbody>
                </template>
              </v-simple-table>
            </td>
          </template>
        </v-data-table>
      </v-card>
    </div>
    <div>
       <v-dialog v-model="update_dialog" persistent max-width="400px">
        <v-card>
          <v-container>
            <v-card-title>
              <span class="headline">Update Bartiny</span>
            </v-card-title>
            <v-card-text>
              <v-form ref="update_form" v-model="valid" lazy-validation>
                <v-row no-gutters>
                  <v-col cols="12">
                     <v-text-field
                      outlined
                      dense
                      readonly=""
                      label="Serial No"
                      v-model="selected_device"
                      required
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12">
                     <v-text-field
                      outlined
                      dense
                      label="AD Number*"
                      :rules="setup_rules.chassis_no"
                      v-model="update_form.chassis_no"
                      required
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12" >
                     <v-text-field
                      outlined
                      dense
                      label="Motherboard Serial No*"
                      :rules="setup_rules.motherboard_no"
                      v-model="update_form.motherboard_no"
                      required
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12" >
                     <v-text-field
                      outlined
                      dense
                      label="IMEI Number"
                      :rules="setup_rules.imei_code"
                      v-model="update_form.imei_code"
                      required
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12" >
                     <v-text-field
                      outlined
                      dense
                      @input="upper()"
                      label="Bluetooth MAC Address*"
                      :rules="setup_rules.bluetooth"
                      v-model="update_form.bluetooth"
                      required
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12" >
                     <v-text-field
                      outlined
                      dense
                      label="Init Code*"
                      :rules="setup_rules.init_code"
                      v-model="update_form.init_code"
                      required
                    ></v-text-field>
                  </v-col>
                </v-row>
              </v-form>
              <small>*indicates required field</small>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="secondary" @click="update_dialog = false">Cancel</v-btn>
              <v-btn color="primary" @click="updateBartiny()">Update</v-btn>
            </v-card-actions>
          </v-container>
        </v-card>
      </v-dialog>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
export default {
  name: "Bartiny",
  data() {
    return {
      show: false,
      valid:true,
       expanded: [],
      singleExpand: false,
      begin_date: new Date().toISOString().substr(0, 10),
      begin_menu: false,
      end_date: new Date().toISOString().substr(0, 10),
      end_menu: false,
      selected_device:'',
      list: true,
      update_dialog:false,
      setup_rules:{
        chassis_no: [ value => !!value || "AD Number required."],
        imei_code: [  value => !!value || "IMEI Number required.",  v => (v && v.length >= 15 && v.length <= 17) || "IMEI should be in min 15 & max 17 Characters"],
        init_code: [  value => !!value || "INIT Code required.",  v => (v && v.length == 10 ) || "INIT code should be 10 Characters"],
        motherboard_no: [  value => !!value || "Motherboard Serial Number required.", ],
        bluetooth: [  value => !!value || "Bluetooth MAC address required.", v => /^([0-9A-Fa-f]{2}[:]){5}([0-9A-Fa-f]{2})$/.test(v) || "Bluetooth MAC address must be valid"],
      },
      update_form:{
          chassis_no:'',
          imei_code:'',
          init_code:'',
          motherboard_no:'',
          bluetooth:'',
          software_version:'',
          firmware_version:''
      },
      headers: [
        {
          text: "Serial No",
          align: "start",
          value: "serial_no",
          filterable: true
        },
        { text: "Model", value: "model" },
        { text: "Bottle Version", value: "bottle_version" },
        { text: "User", value: "paired_user.name" },
        { text: "Created at", value: "created_at" },
        { text: "Latest Event", value: "latest_event.name" },
        { text: "Latest Event Time", value: "latest_event.time" },
        { text: "Actions", value: "actions", sortable: false },
        { text: "", value: "data-table-expand", sortable: false }
      ],
    };
  },
  created () {
    if (this.Bartinys.length == 0) {
      this.$store.dispatch('getBartinysList')
    }
   if (this.Products.length == 0) {
      this.$store.dispatch("getProductsList");
    }
    if (this.ProductModels.length == 0) {
      this.$store.dispatch("getProductModelsList");
    }
  },
  computed: {
    ...mapGetters(['Bartinys', "Products", "ProductModels", "getServerMessage"]),
    bartinys() {
      return this.Bartinys;
    },
    products() {
      const SelectValue = ["All"];
      return SelectValue.concat(this.Products);
    },
    productModels() {
      const SelectValue = ["All"];
      return SelectValue.concat(this.ProductModels);
    },
  },
  methods:{
    updateBartinyDialog(item){
      this.update_dialog = true;
      this.selected_device = item.serial_no;
      this.update_form.chassis_no = item.chassis_no
      this.update_form.imei_code = item.imei_code
      this.update_form.init_code = item.init_code
      this.update_form.motherboard_no = item.motherboard_no
      this.update_form.bluetooth = item.bluetooth
    },
    updateBartiny(){
      if (this.$refs.update_form.validate()) {
        this.$store.dispatch("updateBartiny",[this.update_form,this.selected_device]);
      }
    },
     upper() {
       if(this.update_form.bluetooth){
         this.update_form.bluetooth = this.update_form.bluetooth.toUpperCase()
       }
    }
  },
   watch:{
    getServerMessage: function() {
      if (this.update_dialog) {
        if (
          this.getServerMessage.active &&
          this.getServerMessage.type === "success"
        ) {
          this.update_dialog = false;
          this.$refs.update_form.reset();
        }
      }
    }
  }
};
</script>
