<template>
  <div class="ingredients" style="width:inherit">
    <div class="heading d-flex">
      <div>
        <v-card
          color="accent"
          height="65px"
          width="65px"
          class="d-flex justify-center align-center"
        >
          <img height="35px" width="35px" src="@/assets/icons/ingredients-white.svg" />
        </v-card>
      </div>
      <div style="width:100%;margin-left:10px">
        <v-card height="65px">
          <v-card-title>Requisites</v-card-title>
        </v-card>
      </div>
      <div style="margin-left:10px">
        <v-card
          color="accent"
          height="65px"
          width="65px"
          class="d-flex justify-center align-center"
          @click="toggleView"
        >
          <v-icon color="white">{{ list? 'format_list_bulleted':'apps' }}</v-icon>
        </v-card>
      </div>
    </div>
    <div class="content">
      <v-card class="mt-3 pa-3 pb-0">
        <v-row>
          <v-col class="pb-0">
            <v-text-field
              dense
              label="Search Requisites"
              v-model="search"
              outlined
              append-icon="search"
            ></v-text-field>
          </v-col>
          <v-col class="pb-0">
            <v-select
              dense
              :items="generic_type"
              item-text="name"
              item-value="id"
              label="Select Requisite Group "
              outlined
              v-model="selectedGroup"
              @change="filterIngTypes()"
            ></v-select>
          </v-col>
          <v-col class="pb-0">
            <v-select
              dense
              :items="filteredIngTypes"
              item-text="name"
              item-value="id"
              label="Select Requisites Type"
              outlined
              v-model="selectedType"
              @change="filterIng()"
            ></v-select>
          </v-col>
          <v-col class="pb-0"></v-col>
          <v-col class="pb-0"></v-col>
        </v-row>
      </v-card>
      <v-card v-if="list" style="margin-top:10px">
        <v-data-table
          :headers="headers"
          :search="search"
          :items="filteredIngredients"
          :items-per-page="10"
        >
          <template v-slot:item.imageURI="{ item }">
            <div class="p-2">
              <v-img
                :src="item.img_uri"
                :alt="item.ing_name"
                height="40px"
                width="40px"
                style="border-radius:20px; border:1px solid rgb(0,0,0,.1)"
              ></v-img>
            </div>
          </template>
          <template v-slot:item.quantity="{ item }">
            <v-chip-group column>
              <v-chip v-for="ite in item.quantity" :key="ite">{{ ite }}{{item.quant_units}}</v-chip>
            </v-chip-group>
          </template>
        </v-data-table>
      </v-card>
      <div v-else style="margin-top:10px">
        <v-data-iterator :items="filteredIngredients" :items-per-page.sync="itemsPerPage" :page="page" :search="search" hide-default-footer>
          <template v-slot:default="props">
            <v-row>
              <v-col v-for="item in props.items" :key="item.name" cols="12" sm="6" md="4" lg="3">
                <v-card>
                  <v-card>
                    <v-img :src="item.img_uri" height="250px" style="background:rgb(0,0,0,.1)"></v-img>
                    <v-card-title class="subheading font-weight-bold">{{ item.ing_name }}</v-card-title>
                    <v-card-subtitle>{{item.ing_type_name}}</v-card-subtitle>
                    <v-list dense>
                      <v-list-item>
                        <v-list-item-content>Generic Group:</v-list-item-content>
                        <v-list-item-content class="align-end">{{ item.gen_type }}</v-list-item-content>
                      </v-list-item>

                      <v-list-item>
                        <v-list-item-content>Machine Dispensable:</v-list-item-content>
                        <v-list-item-content class="align-end">{{ item.dispensable }}</v-list-item-content>
                      </v-list-item>

                      <v-list-item>
                        <v-list-item-content>Quantity</v-list-item-content>
                        <v-list-item-content></v-list-item-content>
                      </v-list-item>
                      <v-list-item>
                        <v-chip-group column active-class="primary--text">
                          <v-chip
                            v-for="ite in item.quantity"
                            :key="ite"
                          >{{ ite }}{{item.quant_units}}</v-chip>
                        </v-chip-group>
                      </v-list-item>
                    </v-list>
                    <v-divider></v-divider>
                    <v-card-actions>
                      <v-spacer></v-spacer>
                      <v-btn text>Edit</v-btn>
                    </v-card-actions>
                  </v-card>
                </v-card>
              </v-col>
            </v-row>
          </template>
        </v-data-iterator>
        <template>
          <div class="text-center">
            <v-pagination
              v-model="page"
              :length="numberOfPages"
              prev-icon="mdi-menu-left"
              next-icon="mdi-menu-right"
              @next="next()"
            ></v-pagination>
          </div>
        </template>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  name: "UserRequisites",
  data() {
    return {
      show: false,
      list: true,
      page: 1,
      valid: true,
      image_error:false,
      new_dialog: false,
      itemsPerPage: 12,
      selectedType: "",
      selectedGroup: "",
      search: "",
      url:null,
      file:null,
      headers: [
        { text: "Image", value: "imageURI", sortable: false },
        {
          text: "Requisite Name",
          align: "start",
          value: "ing_name",
          filterable: true
        },
        { text: "Requisite Type", value: "ing_type_name" },
        // { text: "Requisite Group", value: "gen_type" },
        { text: "Machine Dispensable", value: "dispensable" },
        { text: "Quantity", value: "quantity" }
      ],
      filteredIngredients: this.ingredients,
      filteredIngTypes: [],
      new_form: {
        ing_name: "",
        ing_type_id: "",
        quant_units: "",
        quantity: "",
        image: ""
      },
      new_rules: {
        ing_name: [value => !!value || "Requisite name required."],
        ing_type_id: [value => !!value || "Requisite type required."],
        quant_units: [value => !!value || "Measuring Units required."],
        quantity: [v => (v && v.length > 0) || "Quantity Offered Required"],
        image: [
          v => !!v || "Image is required",
          value =>
            !value || value.size < 1000000 || "Image should be less than 2 MB!"
        ]
      },
      default_volumes: [2000, 1000, 750, 700, 500, 300]
    };
  },
  methods: {
    toggleView() {
      this.list = !this.list;
    },
    nextPage() {
      if (this.page + 1 <= this.numberOfPages) this.page += 1;
    },
    formerPage() {
      if (this.page - 1 >= 1) this.page -= 1;
    },
    updateItemsPerPage(number) {
      this.itemsPerPage = number;
    },
    filterIngTypes() {
      this.filteredIngTypes = [];
      this.filteredIngTypes = this.ing_types.filter(type => {
        if (
          type.gen_type_id == this.selectedGroup ||
          this.selectedGroup == "All" ||
          this.selectedGroup === ""
        ) {
          return type;
        }
      });
    },
    filterIng() {
      this.filteredIngredients = [];
      this.filteredIngredients = this.ingredients.filter(ing => {
        if (
          ing.ing_type_id == this.selectedType ||
          this.selectedType == "All" ||
          this.selectedType == ""
        ) {
          return ing;
        }
      });
    },
    addNewIngredientDialog() {
      this.new_dialog = true;
    },
    newIngredient() {
      if (this.$refs.new_form.validate()) {
        let formData = new FormData();
        formData.append('image',this.file)
        formData.append('ing_name',this.new_form.ing_name)
        formData.append('ing_type_id',this.new_form.ing_type_id)
        formData.append('quantity',this.new_form.quantity)
        console.log(this.new_form.quantity)
        this.$store.dispatch("setIngredient", formData);
      } else {
        if(!this.file){
          this.image_error=true
        }
      }
    },
    openFileDialog() {
      document.getElementById("file").click();
    },
    onFileChange(e) {
      this.file = e;
      this.url = URL.createObjectURL(e);
      console.log(e)
    },
    getImgUrl(url) {
      if (url == null) {
        return require("@/assets/image_placeholder.png");
      } else {
        return url;
      }
    },
    remove(item) {
      this.new_form.quantity.splice(this.new_form.quantity.indexOf(item), 1);
      this.new_form.quantity = [...this.new_form.quantity];
    }
  },
  computed: {
    ...mapGetters(["getIngredients", "getIngredientGroups", "getIngredientTypes","getServerMessage"]),
    ingredients() {
      return this.getIngredients;
    },
    generic_type() {
      const SelectValue = ["All"];
      return SelectValue.concat(this.getIngredientGroups);
    },
    ing_types() {
      const SelectValue = ["All"];
      return SelectValue.concat(this.getIngredientTypes);
    },
    numberOfPages() {
      return Math.ceil(this.ingredients.length / this.itemsPerPage);
    }
  },
  created() {
    if (this.getIngredients.length == 0) {
      this.$store.dispatch("getIngredientsList");
    }
    if (this.getIngredientTypes.length == 0) {
      this.$store.dispatch("getIngredientsTypesList");
    }
    if (this.getIngredientGroups.length == 0) {
      this.$store.dispatch("getIngredientGroupsList");
    }

    this.filterIngTypes();
    this.filterIng();
  },
  watch: {
    getIngredients: function() {
      this.filteredIngredients = this.ingredients;
    },
    getServerMessage: function() {
      if (this.new_dialog) {
        if (
          this.getServerMessage.active &&
          this.getServerMessage.type === "success"
        ) {
          this.new_dialog = false;
          this.$refs.new_form.reset();
        }
      }
    }
  },
};
</script>

<style>
.v-image__image {
  background-size: contain;
}
</style>
<style scoped>
#preview {
  display: flex;
  justify-content: center;
  align-items: center;
}

#preview img {
  max-width: 100%;
  max-height: 500px;
}
</style>




