import axios from "axios";

const state = {
  cocktailTypes: [],
};

const getters = {
  getCocktailTypes(state) {
    return state.cocktailTypes;
  },
};

const mutations = {
  setCocktailTypes(state, data) {
    state.cocktailTypes = data;
  },
};

const actions = {
  newCocktailTypes({ commit }, payload) {
    commit("newCocktail", payload);
  },
  getCocktailTypesList({ commit, rootState }) {
    axios
      .get("/cocktail-types", rootState.authentication.accessTokenConfig)
      .then((res) => {
        commit("setCocktailTypes", res.data);
      })
      .catch((error) => {
        console.log(error.response);
      });
  },
  setCocktailType({ commit, dispatch, rootState }, data) {
    axios.post("/cocktail-types/add", data, rootState.authentication.accessFileTokenConfig )
    .then(res => {
        if(res.status == 201){
            commit('setServerMessage', { message:res.data.message, heading:'New cocktail type added', active:true, type:'success' }, { root: true })
            dispatch('getCocktailTypesList')
        }
    })
    .catch(error => {
        commit('setServerMessage', { message:error.response.data.message, heading:'New cocktail type adding failed!', active:true, type:'error' }, { root: true })
        console.log(error.response);
    })
  },
};

export default {
  state,
  mutations,
  actions,
  getters,
};
