<template>
  <div class="ing_types" style="width:inherit">
    <div class="heading d-flex">
      <div>
        <v-card
          color="accent"
          height="65px"
          width="65px"
          class="d-flex justify-center align-center"
        >
          <img height="35px" width="35px" src="@/assets/icons/ingredients-white.svg" />
        </v-card>
      </div>
      <div style="width:100%;margin-left:10px">
        <v-card height="65px">
          <v-card-title>Cocktail Types</v-card-title>
        </v-card>
      </div>
    </div>
    <div class="content">
      <v-card class="mt-3 pa-3 pb-0">
        <v-row class="pb-0">
          <v-col class="pb-0">
            <v-text-field
              dense
              label="Search"
              v-model="search"
              single-line
              outlined
              append-icon="search"
            ></v-text-field>
          </v-col>
          <v-col class="pb-0"></v-col>
          <v-col class="pb-0"></v-col>
          <v-col class="pb-0"></v-col>
          <v-col class="pb-0"></v-col>
        </v-row>
      </v-card>
      <v-card style="margin-top:10px">
        <v-card-title>
          <v-spacer></v-spacer>
          <v-btn depressed color="primary" @click="addNew()">Add New</v-btn>
        </v-card-title>
        <v-data-table
          :headers="headers"
          :search="search"
          :items="getCocktailTypes"
          :items-per-page="10"
        >
          <template v-slot:item.image="{ item }">
            <div class="p-2">
              <v-img
                :src="item.img_uri"
                :alt="item.name"
                height="40px"
                width="40px"
                style="border-radius:20px;"
              ></v-img>
            </div>
          </template>
          <template v-slot:item.actions="{ item }">
            <v-chip-group column>
              <v-chip
                v-for="(i,index) in item.actions"
                :key="index"
                style="text-transform:capitalize"
              >{{ i.name}}</v-chip>
            </v-chip-group>
          </template>
        </v-data-table>
      </v-card>
    </div>
    <div>
      <v-dialog v-model="new_dialog" persistent max-width="500px">
        <v-card>
          <v-container>
            <v-card-title>
              <span class="headline">New Cocktail Type</span>
            </v-card-title>
            <v-card-text>
              <v-form ref="new_form" v-model="valid" lazy-validation>
                <v-row no-gutters>
                  <v-col cols="12">
                    <v-text-field
                      outlined
                      dense
                      label="Cocktail Type Name*"
                      :rules="new_rules.type_name"
                      v-model="new_form.type_name"
                      required
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12">
                    <v-select
                      outlined
                      :items="disp"
                      dense
                      label="Step Dispense Factor*"
                      :rules="new_rules.step_dispense"
                      v-model="new_form.step_dispense"
                      required
                    ></v-select>
                  </v-col>
                  <v-col cols="12">
                    <v-text-field
                      :disabled="!new_form.step_dispense"
                      outlined
                      dense
                      label="Dispense Delay*"
                      :rules="new_rules.dispense_delay"
                      v-model="new_form.dispense_delay"
                      required
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12">
                    <v-row no-gutters="">
                      <v-col cols="11">
                        <div>Add Must required?</div>
                      </v-col>
                      <v-col cols="1">
                        <v-btn @click="addItem()" icon>
                          <v-icon>mdi-plus-circle</v-icon>
                        </v-btn>
                      </v-col>
                    </v-row>
                  </v-col>
                  <v-col cols="12">
                    <v-row no-gutters v-for="(item, index) in new_form.actions" :key="index">
                      <v-col class="pr-md-3">
                        <v-text-field
                          outlined
                          dense
                          label="Action/Requiste Name*"
                          :rules="new_rules.actions.name"
                          v-model="item.name"
                          required
                        ></v-text-field>
                      </v-col>
                      <v-col class="pr-md-3">
                        <v-autocomplete
                          outlined
                          :items="getRequisites"
                          dense
                          item-text="ing_name"
                          item-value="id"
                          label="Requisite*"
                          :rules="new_rules.actions.req_id"
                          v-model="item.req_id"
                          required
                        ></v-autocomplete>
                      </v-col>
                      <v-col cols="1">
                        <v-btn @click="removeItem(index)" icon>
                          <v-icon>mdi-delete</v-icon>
                        </v-btn>
                      </v-col>
                    </v-row>
                  </v-col>
                </v-row>
              </v-form>
              <small>*indicates required field</small>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="secondary" @click="new_dialog = false">Cancel</v-btn>
              <v-btn color="primary" @click="newItem()">Add</v-btn>
            </v-card-actions>
          </v-container>
        </v-card>
      </v-dialog>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "CocktailTypes",
  data() {
    return {
      show: false,
      search: "",
      list: true,
      headers: [
        { text: "Type Name", value: "type_name" },
        { text: "Step Dispense", value: "step_dispense" },
        { text: "Dispensing Delay", value: "dispense_delay" },
        { text: "Must required", value: "actions" }
      ],
      item: "",
      itemsPerPage: 12,
      page: 1,
      new_form: {
        type_name: "",
        step_dispense: null,
        dispense_delay: 0,
        actions: [{ name: "", req_id: null }]
      },
      new_rules: {
        type_name: [value => !!value || "Cocktail type name required."],
        step_dispense: [
          value => !!value || value == false || "Step Dispense required"
        ],
        dispense_delay: [v => !!v || v == 0 || "Dispense delay required"],
        actions: {
          name: [value => !!value || "Must required action name required."],
          req_id: [value => !!value || "requisite required."]
        }
      },
      valid: true,
      new_dialog: false,
      disp: [
        { text: "True", value: true },
        { text: "False", value: false }
      ]
    };
  },
  created() {
    if (this.getCocktailTypes.length == 0) {
      this.$store.dispatch("getCocktailTypesList");
    }
    if (this.getRequisites.length == 0) {
      this.$store.dispatch("getRequisitesList");
    }
  },
  computed: {
    ...mapGetters(["getCocktailTypes", "getServerMessage", "getRequisites"])
  },
  methods: {
    toggleView() {
      this.list = !this.list;
    },
    newItem() {
      if (this.$refs.new_form.validate()) {
        this.$store.dispatch("setCocktailType", this.new_form);
      }
    },
    addNew() {
      this.new_dialog = true;
    },
    getActions(item) {
      return JSON.parse(item);
    },
    removeItem(index) {
      if (index > -1) {
        this.new_form.actions.splice(index, 1);
      }
    },
    addItem() {
      this.new_form.actions.push({ name: "", req_id: null });
    }
  },
  watch: {
    getServerMessage: function() {
      if (this.new_dialog) {
        if (
          this.getServerMessage.active &&
          this.getServerMessage.type === "success"
        ) {
          this.new_dialog = false;
          this.$refs.new_form.reset();
        }
      }
    }
  }
};
</script>
