<template>
  <div>
    <v-card class="mx-auto" width="500">
      <div style="padding:30px;width:100%">
        <div class="center">
          <img src="@/assets/logo-cap.svg" height="200px" />
        </div>
        <v-form ref="form" v-model="valid" lazy-validation>
          <div class="center">
            <p class="title font-weight-regular" style="margin-top:0px">Resend Confirmation Link</p>
          </div>
          <div style="width:100%">
            <v-text-field
              outlined
              label="Email"
              prepend-inner-icon="email"
              :rules="emailRules"
              required
              v-model="email"
            ></v-text-field>
          </div>
          <v-row align="center">
            <v-col class="text-center" cols="12">
              <v-btn color="primary" @click="validate">Submit</v-btn>
            </v-col>
          </v-row>
          <v-row>
            <v-col>
              <v-alert outlined :type="type" v-if="message != null">{{ message }}</v-alert>
            </v-col>
          </v-row>
        </v-form>
        <v-row align="center">
          <v-col cols="6">
            <router-link class="link" small text to="/auth">Login? Click here</router-link>
          </v-col>
          <v-col class="text-right" cols="6">
            <router-link class="link" small text to="/">Home</router-link>
          </v-col>
        </v-row>
      </div>
    </v-card>
  </div>
</template>

<script>
import axios from "axios";
  export default {
    name: 'ResendConfirm',
    data() {
      return {
        message:null,
        type:null,
        email:null,
        valid: true,
        emailRules: [
        v => !!v || "E-mail is required",
        v => /.+@.+\..+/.test(v) || "E-mail must be valid"
      ]
      }
    },
   
    methods: {
        validate() {
            if (this.$refs.form.validate()) {
                axios.post(`/resend-confirmation/${this.email}`).then(res => {
                console.log(res);
                this.message = res.response.data.message
                this.type = 'success'

                })
                .catch(error => {
                console.log(error.response);
                this.message = error.response.data.message
                this.type = 'error'
                });
            }
        }
    }
  }
</script>

<style scoped>
  .center {
  text-align: center;
  }

  .link {
    text-transform: Capitalize;
    text-decoration: none;
    font-weight: 400;
    font-size: 12px;
  }

  h5 {
    font-weight: 400;
  }
</style>