import axios from "axios";

const state = {
  requisites: [],
};

const getters = {
  getRequisites(state) {
    return state.requisites;
  },
};

const mutations = {
  newRequisite(state, payload) {
    state.requisites.push(payload);
    return "success";
  },
  setRequisites(state, data) {
    state.requisites = data;
  },
};

const actions = {
  setRequisite({ commit, dispatch, rootState }, data) {
  
    axios.post("/requisites/add", data, rootState.authentication.accessFileTokenConfig )
    .then(res => {
        if(res.status == 201){
            commit('setServerMessage', { message:res.data.message, heading:'New requisite added', active:true, type:'success' }, { root: true })
            dispatch('getRequisitesList')
        }
    })
    .catch(error => {
        commit('setServerMessage', { message:error.response.data.message, heading:'New requisite adding failed!', active:true, type:'error' }, { root: true })
        console.log(error.response);
    })
  },
  getRequisitesList({ commit, rootState }) {
    axios
      .get("/requisites", rootState.authentication.accessTokenConfig)
      .then((res) => {
        commit("setRequisites", res.data);
      })
      .catch((error) => {
        console.log(error.response);
      });
  }
};

export default {
  state,
  mutations,
  actions,
  getters,
};
