<template>
  <div id="inspire">
    <v-navigation-drawer v-model="drawer" app clipped>
      <v-list-item two-line="">
        <v-list-item-avatar>
          <img src="@/assets/logo.png" />
        </v-list-item-avatar>
        <v-list-item-content>
          <v-list-item-title>{{ User.name }}</v-list-item-title>
          <v-list-item-subtitle>{{ User.email }}</v-list-item-subtitle>
        </v-list-item-content>
      </v-list-item>
      <v-divider></v-divider>

      <v-list dense nav>
        <v-list-item-group v-model="item" color="primary">
          <v-list-item v-for="item in items" :key="item.title" :to="item.route" exact>
            <v-list-item-icon>
              <v-icon>{{ item.icon }}</v-icon>
            </v-list-item-icon>
            <v-list-item-content>
              <v-list-item-title>{{ item.title }}</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </v-list-item-group>
      </v-list>
    </v-navigation-drawer>

    <v-app-bar app clipped-left>
      <v-app-bar-nav-icon @click.stop="drawer = !drawer" />
      <div style="background:grey;border-radius:50%; height:50px;width:50px;margin-right:10px">
        <img style="height:50px;width:50px" src="@/assets/logo.png" />
      </div>
      <v-toolbar-title>Bar Manager Dashboard</v-toolbar-title>
    </v-app-bar>

    <v-content>
      <v-container class="fill-height" fluid>
        <router-view />
      </v-container>
    </v-content>

    <v-footer app>
      <span>&copy; 2019</span>
    </v-footer>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import router from "../router";
export default {
  name: "User",

  components: {},

  data() {
    return {
      drawer: true,
      items: [
        { title: "Overview", icon: "mdi-view-dashboard", route: "/user" },
        {
          title: "Analytics",
          icon: "insert_chart_outlined",
          route: "/user/analytics"
        },
        { title: "My Devices", icon: "devices_other", route: "/user/devices" },
        {
          title: "Requisites",
          icon: "mdi-cupboard-outline",
          route: "/user/requisites"
        },
        { title: "Cocktails", icon: "mdi-glass-wine", route: "/user/cocktails" },
        { title: "Inventory", icon: "storage", route: "/user/inventory" },
        { title: "Support", icon: "help_outline", route: "/user/support" },
        { title: "Settings", icon: "settings", route: "/user/settings" },
        { title: "Logout", icon: "lock", route: "/auth/logout" }
      ],
      color: "primary",
      colors: ["primary", "blue", "success", "red", "teal"],
      right: false,
      miniVariant: false,
      expandOnHover: false,
      background: false,
      countDown: 30,
      pageActive: true,
      counter: 0,
      timer1: null,
      pageActiveTimer: null,
      item:''
    };
  },
 created() {
    if (this.isAuthenticated) {
      this.$store.dispatch("tryAutoLogin");
    }
  },
  watch: {
    logoutDialog: function() {
      if (this.logoutDialog) {
        clearInterval(this.timer1);
        this.countDown = 30;
        this.countDownTimer();
      }
    }
  },
  methods: {
    logout() {
      this.$store.dispatch("closeLogoutDialog");
      router.replace("/auth/logout");
      clearInterval(this.timer1);
    },
     closeDialog() {
      this.$store.dispatch("clearMessage");
    },
    stay() {
      this.$store.dispatch("closeLogoutDialog");
      this.$store.dispatch("reLogin");
      clearInterval(this.timer1);
    },
    countDownTimer() {
      this.timer1 = setInterval(() => {
        if (this.countDown > 0) {
          this.countDown--;
        } else {
          this.logout();
        }
      }, 1000);
    },
    checkPageActive() {
      this.pageActiveTimer = setInterval(() => {
        this.counter++;
        console.log(this.counter);
        if (this.counter > 890) {
          this.pageActive = false;
        }
      }, 1000);
    }
  },
  computed: {
    ...mapGetters(["User", "logoutDialog", "isAuthenticated","getServerMessage"])
  }
};
</script>
