<template>
  <div>
    <v-card class="mx-auto" width="500">
      <v-form ref="form" v-model="valid" lazy-validation>
        <div style="padding:30px;width:100%">
          <div class="center">
            <img src="@/assets/logo-cap.svg" height="200px" />
          </div>
          <div class="center">
            <p class="title font-weight-regular" style="margin-top:0px">Enter New Password</p>
          </div>
          <div style="width:100%">
            <v-text-field
              outlined
              :rules="passwordRules"
              v-model="form.password"
              label="New Password"
              prepend-inner-icon="email"
              type="password"
            ></v-text-field>
          </div>
          <div style="width:100%">
            <v-text-field
              outlined
              v-model="confirm_password"
              label="Confirm Password"
              :rules="[ v => !!v || 'Password is required', (form.password === confirm_password) || 'Password must match']"
              prepend-inner-icon="email"
              type="password"
            ></v-text-field>
          </div>
          <v-row align="center">
            <v-col class="text-center" cols="12">
              <v-btn @click="reset()" color="primary">Submit</v-btn>
            </v-col>
          </v-row>

          <v-row align="center">
            <v-col class="text-center" cols="112">
              <router-link class="link" small text to="/auth">Already Registered? Login Here</router-link>
            </v-col>
          </v-row>
          <v-row justify="center">
          <v-dialog v-model="authMessage.dialog" persistent max-width="350">
            <v-card>
              <v-card-title
                class="title center"
                style="justify-content:center"
              >{{ authMessage.heading }}</v-card-title>
              <v-card-text class="font-weight-regular center body-1">{{ authMessage.msg }}</v-card-text>
              <v-card-actions style="justify-content:center">
                <v-btn text @click="closeDialog">close</v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-row>
        </div>
      </v-form>
    </v-card>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "Reset",
  data() {
    return {
      message: null,
      type: null,
       valid: true,
      dialog: false,
      confirm_password:'',
      form: {
        reset_id: this.$route.params.id,
        password: ""
      },
      passwordRules: [ 
            v => !!v || 'Password is required', 
            v => (v && v.length >= 8) || 'Password must have 8+ characters',
            v => /(?=.*[A-Z])/.test(v) || 'Must have one uppercase character', 
            v => /(?=.*\d)/.test(v) || 'Must have one number', 
            v => /([!@$%])/.test(v) || 'Must have one special character [!@#$%]' 
          ],
      confirm_rules:''
    };
  },
  computed: {
      ...mapGetters(["authMessage"])
    },
  methods: {
    reset() {
       this.$store.dispatch("resetPassword",this.form)
    },
     closeDialog() {
      if (this.authMessage.type == "success") this.$refs.form.reset();
      this.$store.dispatch("resetMessage");
    }
  }
};
</script>

<style scoped>
.center {
  text-align: center;
}

.link {
  text-transform: Capitalize;
  text-decoration: none;
  font-weight: 400;
  font-size: 12px;
}

h5 {
  font-weight: 400;
}
</style>