<template>
  <div class="Devices" style="width:inherit">
    <div class="heading d-flex">
      <div>
        <v-card
          color="accent"
          height="65px"
          width="65px"
          class="d-flex justify-center align-center"
        >
          <img height="35px" width="35px" src="@/assets/icons/device-white.svg" />
        </v-card>
      </div>
      <div style="width:100%;margin-left:10px">
        <v-card height="65px">
          <v-card-title>Products Catalogue</v-card-title>
        </v-card>
      </div>
    </div>
    <div class="content">
      <v-card style="margin-top:10px">
        <v-card-title>
          Products
          <v-spacer></v-spacer>
          <v-btn depressed color="primary" @click="addNewProductDialog()">Add New</v-btn>
        </v-card-title>
        <v-data-table :headers="product_headers" :items="products" :items-per-page="10">
          <template v-slot:item.actions="{ item }">
            <v-menu bottom origin="center center" transition="scale-transition">
              <template v-slot:activator="{ on }">
                <v-btn icon v-on="on">
                  <v-icon>mdi-dots-vertical</v-icon>
                </v-btn>
              </template>

              <v-list dense>
                <v-list-item-group color="primary">
                  <v-list-item @click="editProduct(item)">
                    <v-list-item-icon>
                      <v-icon>edit</v-icon>
                    </v-list-item-icon>
                    <v-list-item-content>
                      <v-list-item-title>Edit</v-list-item-title>
                    </v-list-item-content>
                  </v-list-item>
                </v-list-item-group>
                <v-list-item-group color="primary">
                  <v-list-item @click="deleteProduct(item)">
                    <v-list-item-icon>
                      <v-icon>delete</v-icon>
                    </v-list-item-icon>
                    <v-list-item-content>
                      <v-list-item-title>Delete</v-list-item-title>
                    </v-list-item-content>
                  </v-list-item>
                </v-list-item-group>
              </v-list>
            </v-menu>
          </template>
        </v-data-table>
      </v-card>
      <v-row align="center">
        <v-col class="text-center" cols="12"></v-col>
      </v-row>
      <v-card style="margin-top:10px">
        <v-card-title>
          Products Models
          <v-spacer></v-spacer>
          <v-btn depressed color="primary" @click="addNewProductModelDialog()">Add New</v-btn>
        </v-card-title>
        <v-data-table
          :headers="models_headers"
          :items="deviceModels"
          :items-per-page="10"
          :single-expand="singleExpand"
          :expanded.sync="expanded"
          item-key="model_no"
          show-expand
        >
          <template v-slot:item.actions="{ item }">
            <v-menu bottom origin="center center" transition="scale-transition">
              <template v-slot:activator="{ on }">
                <v-btn icon v-on="on">
                  <v-icon>mdi-dots-vertical</v-icon>
                </v-btn>
              </template>

              <v-list dense>
                <v-list-item-group color="primary">
                  <v-list-item @click="editProduct(item)">
                    <v-list-item-icon>
                      <v-icon>edit</v-icon>
                    </v-list-item-icon>
                    <v-list-item-content>
                      <v-list-item-title>Edit</v-list-item-title>
                    </v-list-item-content>
                  </v-list-item>
                </v-list-item-group>
                <v-list-item-group color="primary">
                  <v-list-item @click="deleteProduct(item)">
                    <v-list-item-icon>
                      <v-icon>delete</v-icon>
                    </v-list-item-icon>
                    <v-list-item-content>
                      <v-list-item-title>Delete</v-list-item-title>
                    </v-list-item-content>
                  </v-list-item>
                </v-list-item-group>
              </v-list>
            </v-menu>
          </template>
          <template v-slot:expanded-item="{ headers, item }">
            <td :colspan="headers.length" style="padding:0px">
              <v-simple-table style="margin: 12px">
                <template v-slot:default>
                  <thead>
                    <tr>
                      <th colspan="2" class="text-center">Technical Details</th>
                    </tr>
                  </thead>
                  <tbody>
                     <tr >
                      <td style="width:250px">Firmware Version</td> <td>{{ item.firmware_version }}</td>
                    </tr>
                     <tr>
                      <td style="width:250px">Hardware Verison</td> <td>{{ item.hardware_version }}</td>
                    </tr>
                     <tr>
                      <td style="width:250px">Software Version</td> <td>{{ item.software_version }}</td>
                    </tr>
                     <tr>
                      <td style="width:250px">API Version</td> <td>{{ item.api_version }}</td>
                    </tr>
                     <tr>
                      <td style="width:250px">Revision</td> <td>{{ item.rev_code }}</td>
                    </tr>
                  </tbody>
                </template>
              </v-simple-table>
            </td>
          </template>
        </v-data-table>
      </v-card>
    </div>
    <div>
      <v-dialog v-model="product_dialog" persistent max-width="400px">
        <v-card>
           <v-container>
          <v-card-title>
            <span class="headline">Create New Product</span>
          </v-card-title>
          <v-card-text>
           
              <v-form ref="product_form" v-model="valid" lazy-validation>
                <v-row no-gutters>
                  <v-col cols="12">
                    <v-text-field
                      outlined
                      dense
                      label="Product Name*"
                      :rules="product_rules.name"
                      v-model="product_form.name"
                      required
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12">
                    <v-text-field
                      outlined
                      dense
                      label="Product Code*"
                      :rules="product_rules.code"
                      v-model="product_form.code"
                      required
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12">
                    <v-textarea
                      outlined
                      dense
                      label="Description*"
                      :rules="product_rules.description"
                      v-model="product_form.description"
                      required
                    ></v-textarea>
                  </v-col>
                </v-row>
              </v-form>
            
            <small>*indicates required field</small>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="secondary" @click="product_dialog = false">Cancel</v-btn>
            <v-btn color="primary" @click="newProduct()">Create</v-btn>
          </v-card-actions>
          </v-container>
        </v-card>
      </v-dialog>
      <v-dialog v-model="model_dialog" persistent max-width="500px">
        <v-card>
           <v-container>
          <v-card-title>
            <span class="headline">Create New Product Model</span>
          </v-card-title>
          <v-card-text>
           
              <v-form ref="model_form" v-model="model_valid" lazy-validation>
                <v-row no-gutters>
                  <v-col cols="12" sm="6" class="pr-sm-3">
                    <v-select outlined dense
                  :items="products"
                  label="Product"
                  item-text="name"
                  item-value="id"
                   v-model="model_form.product_id"
                  required
                ></v-select>
                  </v-col>
                  <v-col cols="12" sm="6" >
                    <v-text-field
                      outlined
                      dense
                      label="Revision*"
                      :rules="model_rules.revision"
                      v-model="model_form.revision"
                      required
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6" class="pr-sm-3">
                    <v-text-field
                      outlined
                      dense
                      label="Model Code*"
                      :rules="model_rules.model_code"
                      v-model="model_form.model_code"
                      required
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6">
                    <v-text-field
                      outlined
                      dense
                      label="Model No*"
                      :rules="model_rules.model_no"
                      v-model="model_form.model_no"
                      required
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6" class="pr-sm-3">
                    <v-text-field
                      outlined
                      dense
                      label="API Version*"
                      :rules="model_rules.api_version"
                      v-model="model_form.api_version"
                      required
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6">
                    <v-text-field
                      outlined
                      dense
                      label="Software Version*"
                      :rules="model_rules.software_version"
                      v-model="model_form.software_version"
                      required
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6" class="pr-sm-3">
                    <v-text-field
                      outlined
                      dense
                      label="Hardware Version*"
                      :rules="model_rules.hardware_revision"
                      v-model="model_form.hardware_revision"
                      required
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6">
                    <v-text-field
                      outlined
                      dense
                      label="Firmware Version*"
                      :rules="model_rules.firmware_version"
                      v-model="model_form.firmware_version"
                      required
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12">
                    <v-textarea
                      outlined
                      dense
                      label="Description*"
                      :rules="model_rules.description"
                      v-model="model_form.description"
                      required
                    ></v-textarea>
                  </v-col>
                </v-row>
              </v-form>
            
            <small>*indicates required field</small>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="secondary" @click="model_dialog = false">Cancel</v-btn>
            <v-btn color="primary" @click="newProductModel()">Create</v-btn>
          </v-card-actions>
          </v-container>
        </v-card>
      </v-dialog>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "Products",
  data() {
    return {
      show: false,
      begin_date: new Date().toISOString().substr(0, 10),
      begin_menu: false,
      end_date: new Date().toISOString().substr(0, 10),
      end_menu: false,
      list: true,
      valid: true,
      model_valid:true,
      dialog: false,
      product_dialog: false,
      model_dialog: false,
      product_form: {
        name: "",
        code: "",
        description: ""
      },
      model_form: {
        product_id: null,
        model_code: "",
        model_no: "",
        description: "",
        api_version: "",
        firmware_version: "",
        hardware_revision: "",
        software_version: "",
        revision: "",
      },
      product_rules: {
        name: [value => !!value || "Product Name Required."],
        code: [
          value => !!value || "Product Code Required.",
          v =>
            (v && v.length >= 1 && v.length <= 3) ||
            "Product code should be in min 1 & max 3 letters"
        ],
        description: [value => !!value || "Product Description Required."]
      },
       model_rules: {
        product: [value => !!value || "Product Required."],
        code: [
          value => !!value || "Model Code Required.",
          v =>
            (v && v.length >= 1 && v.length <= 3) ||
            "Model code should be in min 1 & max 3 letters"
        ],
        model_no: [
          value => !!value || "Model No Required.",
        ],
        description: [value => !!value || "Product Description Required."],
        api_version: [
          value => !!value || "API Version Required.",
          v =>
            (v && v.length >= 1 && v.length <= 3) ||
            "API Version should be in min 1 & max 3 letters"
        ],
        firmware_version: [
          value => !!value || "Firmware Version Required.",
          v =>
            (v && v.length >= 1 && v.length <= 3) ||
            "Firmware Version should be in min 1 & max 3 letters"
        ],
        hardware_revision: [
          value => !!value || "Hardware Version Required.",
          v =>
            (v && v.length >= 1 && v.length <= 3) ||
            "Hardware Version should be in min 1 & max 3 letters"
        ],
         software_version: [
          value => !!value || "Software Version Required.",
          v =>
            (v && v.length >= 1 && v.length <= 3) ||
            "Software Version should be in min 1 & max 3 letters"
        ],
        revision: [
          value => !!value || "Software Version Required.",
          v =>
            (v && v.length >= 1 && v.length <= 3) ||
            "Revision should be in min 1 & max 3 letters"
        ],
      },
      product_headers: [
        {
          text: "Product",
          align: "start",
          value: "name",
          filterable: true
        },
        { text: "Product Code", value: "code" },
        { text: "Description", value: "description" },
        { text: "Actions", value: "actions", sortable: false }
      ],
      models_headers: [
        { text: "Product", align: "start", value: "product", filterable: true },
        { text: "Model No", value: "model_no" },
        { text: "Description", value: "description" },
        { text: "Actions", value: "actions", sortable: false },
        { text: "", value: "data-table-expand", sortable: false }
      ],
      expanded: [],
      singleExpand: false,
    };
  },
  created() {
    if (this.Products.length == 0) {
      this.$store.dispatch("getProductsList");
    }
    if (this.ProductModels.length == 0) {
      this.$store.dispatch("getProductModelsList");
    }
  },
  computed: {
    ...mapGetters(["Devices", "Products", "ProductModels", "getServerMessage"]),
    products() {
      return this.Products;
    },
    deviceModels() {
      return this.ProductModels;
    }
  },
  methods: {
    newProduct() {
      if (this.$refs.product_form.validate()) {
        this.$store.dispatch("setProduct", this.product_form);
      }
    },
    newProductModel() {
    if (this.$refs.model_form.validate()) {
      this.$store.dispatch("setProductModel", this.model_form);
    }
    },
    editProduct(item) {
      console.log(item);
    },
    deleteProduct(item) {
      console.log(item);
    },
    addNewProductDialog() {
      this.product_dialog = true;
    },
     addNewProductModelDialog() {
      this.model_dialog = true;
    }
  },
  watch: {
    getServerMessage: function() {
      if (this.product_dialog) {
        if (
          this.getServerMessage.active &&
          this.getServerMessage.type === "success"
        ) {
          this.product_dialog = false;
          this.$refs.product_form.reset();
        }
      }

      if (this.model_dialog) {
        if (
          this.getServerMessage.active &&
          this.getServerMessage.type === "success"
        ) {
          this.model_dialog = false;
          this.$refs.model_form.reset();
        }
      }
    }
  }
};
</script>
