<template>
  <div class="Devices" style="width:inherit">
    <div class="heading d-flex">
      <div>
        <v-card
          color="accent"
          height="65px"
          width="65px"
          class="d-flex justify-center align-center"
        >
          <img height="35px" width="35px" src="@/assets/icons/device-white.svg" />
        </v-card>
      </div>
      <div style="width:100%;margin-left:10px">
        <v-card height="65px">
          <v-card-title>Devices</v-card-title>
        </v-card>
      </div>
    </div>
    <div class="content">
      <v-card class="mt-3 pa-3 pb-0">
        <v-row class="pb-0">
          <v-col class="pb-0" cols="12" sm="4" md="3">
            <v-text-field dense label="Search Serial No" single-line outlined append-icon="search"></v-text-field>
          </v-col>
          <v-col class="pb-0" cols="12" sm="4" md="3">
            <v-select
              dense
              :items="products"
              item-value="code"
              item-text="name"
              label="Select Product"
              outlined
            ></v-select>
          </v-col>
          <v-col class="pb-0" cols="12" sm="4" md="2">
            <v-select
              dense
              :items="productModels"
              item-text="model_no"
              item-value="model_no"
              label="Select Model"
              outlined
            ></v-select>
          </v-col>
          <v-col class="pb-0" cols="12" sm="4" md="2">
            <v-menu
              ref="begin_menu"
              v-model="begin_menu"
              :close-on-content-click="false"
              :return-value.sync="begin_date"
              transition="scale-transition"
              offset-y
              min-width="290px"
            >
              <template v-slot:activator="{ on }">
                <v-text-field
                  outlined
                  dense
                  v-model="begin_date"
                  label="Begin Date"
                  prepend-inner-icon="event"
                  v-on="on"
                ></v-text-field>
              </template>
              <v-date-picker v-model="begin_date" no-title scrollable>
                <v-spacer></v-spacer>
                <v-btn text color="primary" @click="begin_menu = false">Cancel</v-btn>
                <v-btn text color="primary" @click="$refs.begin_menu.save(begin_date)">OK</v-btn>
              </v-date-picker>
            </v-menu>
          </v-col>
          <v-col class="pb-0" cols="12" sm="4" md="2">
            <v-menu
              ref="end_menu"
              v-model="end_menu"
              :close-on-content-click="false"
              :return-value.sync="end_date"
              transition="scale-transition"
              offset-y
              min-width="290px"
            >
              <template v-slot:activator="{ on }">
                <v-text-field
                  outlined
                  dense
                  v-model="end_date"
                  label="End Date"
                  prepend-inner-icon="event"
                  v-on="on"
                ></v-text-field>
              </template>
              <v-date-picker v-model="end_date" no-title scrollable>
                <v-spacer></v-spacer>
                <v-btn text color="primary" @click="end_menu = false">Cancel</v-btn>
                <v-btn text color="primary" @click="$refs.end_menu.save(end_date)">OK</v-btn>
              </v-date-picker>
            </v-menu>
          </v-col>
        </v-row>
      </v-card>
      <v-card style="margin-top:10px">
        <v-card-title>
          <v-spacer></v-spacer>
          <v-btn depressed color="primary" @click="addNewDeviceDialog()">Add New</v-btn>
        </v-card-title>
        <v-data-table :headers="headers" :items="devices" :items-per-page="10" >
          <template v-slot:item.actions="{ item }">
            <v-menu bottom origin="center center" transition="scale-transition">
              <template v-slot:activator="{ on }">
                <v-btn icon v-on="on">
                  <v-icon>mdi-dots-vertical</v-icon>
                </v-btn>
              </template>

              <v-list dense>
                <v-list-item-group color="primary">
                  <v-list-item @click="editDevice(item)">
                    <v-list-item-icon>
                      <v-icon>edit</v-icon>
                    </v-list-item-icon>
                    <v-list-item-content>
                      <v-list-item-title>Edit</v-list-item-title>
                    </v-list-item-content>
                  </v-list-item>
                </v-list-item-group>
                <v-list-item-group v-if="item.latest_event.name == 'Created'" color="primary">
                  <v-list-item @click="setupDeviceDialog(item)">
                    <v-list-item-icon>
                      <v-icon>phonelink_setup</v-icon>
                    </v-list-item-icon>
                    <v-list-item-content>
                      <v-list-item-title>Setup Device</v-list-item-title>
                    </v-list-item-content>
                  </v-list-item>
                </v-list-item-group>
              </v-list>
            </v-menu>
          </template>
        </v-data-table>
      </v-card>
    </div>
    <div>
      <v-dialog v-model="dialog" persistent max-width="400px">
        <v-card>
          <v-container>
            <v-card-title>
              <span class="headline">Create New Device</span>
            </v-card-title>
            <v-card-text>
              <v-form ref="form" v-model="valid" lazy-validation>
                <v-row no-gutters>
                  <v-col cols="12">
                    <v-select
                      outlined
                      dense
                      :items="Products"
                      label="Select Product"
                      item-text="name"
                      item-value="id"
                      v-model="form.product_id"
                      :rules="rules.product"
                      @change="filteredproductModels()"
                      required
                    ></v-select>
                  </v-col>
                  <v-col cols="12" >
                    <v-select
                      outlined
                      dense
                      :items="selectModels"
                      label="Select Product Model"
                      item-text="model_no"
                      item-value="id"
                      :rules="rules.model_no"
                      required
                      v-model="form.model_id"
                    ></v-select>
                  </v-col>
                </v-row>
              </v-form>

              <small>*indicates required field</small>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="secondary" @click="dialog = false">Cancel</v-btn>
              <v-btn color="primary" @click="newDevice()">Create</v-btn>
            </v-card-actions>
          </v-container>
        </v-card>
      </v-dialog>
      <v-dialog v-model="setup_dialog" persistent max-width="400px">
        <v-card>
          <v-container>
            <v-card-title>
              <span class="headline">Create New Device</span>
            </v-card-title>
            <v-card-text>
              <v-form ref="setup_form" v-model="valid" lazy-validation>
                <v-row no-gutters>
                  <v-col cols="12">
                     <v-text-field
                      outlined
                      dense
                      readonly=""
                      label="Serial No"
                      v-model="selected_device"
                      required
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12">
                     <v-text-field
                      outlined
                      dense
                      label="AD Number*"
                      :rules="setup_rules.chassis_no"
                      v-model="setup_form.chassis_no"
                      required
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12" >
                     <v-text-field
                      outlined
                      dense
                      label="Motherboard Serial No*"
                      :rules="setup_rules.motherboard_no"
                      v-model="setup_form.motherboard_no"
                      required
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12" >
                     <v-text-field
                      outlined
                      dense
                      label="IMEI Number"
                      :rules="setup_rules.imei_code"
                      v-model="setup_form.imei_code"
                      required
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12" >
                     <v-text-field
                      outlined
                      dense
                      @input="upper()"
                      label="Bluetooth MAC Address*"
                      :rules="setup_rules.bluetooth"
                      v-model="setup_form.bluetooth"
                      required
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12" >
                     <v-text-field
                      outlined
                      dense
                      label="Init Code*"
                      :rules="setup_rules.init_code"
                      v-model="setup_form.init_code"
                      required
                    ></v-text-field>
                  </v-col>
                </v-row>
              </v-form>
              <small>*indicates required field</small>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="secondary" @click="setup_dialog = false">Cancel</v-btn>
              <v-btn color="primary" @click="setupDevice()">Create</v-btn>
            </v-card-actions>
          </v-container>
        </v-card>
      </v-dialog>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "Devices",
  data() {
    return {
      show: false,
      begin_date: new Date().toISOString().substr(0, 10),
      begin_menu: false,
      end_date: new Date().toISOString().substr(0, 10),
      end_menu: false,
      list: true,
      dialog: false,
      setup_dialog: false,
      valid: true,
      selectModels:[],
      selected_device:null,
      form: {
        product_id:null,
        model_id: null
      },
      setup_form:{
          chassis_no:'12345',
          imei_code:'12345',
          init_code:'12345',
          motherboard_no:'12345',
          bluetooth:'12345'
      },
       setup_rules:{
        chassis_no: [ value => !!value || "AD Number required."],
        imei_code: [  value => !!value || "IMEI Number required.",  v => (v && v.length >= 15 && v.length <= 17) || "IMEI should be in min 15 & max 17 Characters"],
        init_code: [  value => !!value || "INIT Code required.",  v => (v && v.length == 10 ) || "INIT code should be 10 Characters"],
        motherboard_no: [  value => !!value || "Motherboard Serial Number required.", ],
        bluetooth: [  value => !!value || "Bluetooth MAC address required.", v => /^([0-9A-Fa-f]{2}[:]){5}([0-9A-Fa-f]{2})$/.test(v) || "Bluetooth MAC address must be valid"],
      },
      rules:{
        product: [ value => !!value || "Product required."],
        model_no: [
          value => !!value || "Model No required.",
        ],
      },
      headers: [
        { text: "Serial No", align: "start", value: "serial_no", filterable: true },
        { text: "Model", value: "model_no" },
        { text: "Product", value: "product" },
        { text: "User", value: "paired_user.name" },
        { text: "Created at", value: "created_at" },
        { text: "Latest Event", value: "latest_event.name" },
        { text: "Latest Event Time", value: "latest_event.time" },
        { text: "Actions", value: "actions", sortable: false }
      ]
    };
  },
  created() {
    if (this.Devices.length == 0) {
      this.$store.dispatch("getDevicesList");
    }
    if (this.Products.length == 0) {
      this.$store.dispatch("getProductsList");
    }
    if (this.ProductModels.length == 0) {
      this.$store.dispatch("getProductModelsList");
    }
  },
  computed: {
    ...mapGetters(["Devices", "Products", "ProductModels", "getServerMessage"]),
    devices() {
      return this.Devices;
    },
    products() {
      const SelectValue = ["All"];
      return SelectValue.concat(this.Products);
    },
    productModels() {
      const SelectValue = ["All"];
      return SelectValue.concat(this.ProductModels);
    },
    
  },
  methods: {
    addNewDeviceDialog() {
      this.dialog = true;
    },
    filteredproductModels() {
       this.selectModels = [];
       for (let i = 0; i < this.productModels.length; i++) {
         if (this.productModels[i].product_id === this.form.product_id) {
           this.selectModels.push(this.productModels[i])
         }
       }
    },
    newDevice() {
       if (this.$refs.form.validate()) {
        this.$store.dispatch("setDevice",{ model_id:this.form.model_id });
      }
    },
    setupDevice(){
       if (this.$refs.setup_form.validate()) {
        this.$store.dispatch("setupDevice",[this.setup_form,this.selected_device]);
      }
    },
    editDevice(){},
    setupDeviceDialog(item){
      this.setup_dialog = true;
      this.selected_device = item.serial_no;
    },
    upper() {
      if( this.setup_form.bluetooth){
        this.setup_form.bluetooth = this.setup_form.bluetooth.toUpperCase()
      }
    }
  },
  watch:{
    getServerMessage: function() {
      if (this.dialog) {
        if (
          this.getServerMessage.active &&
          this.getServerMessage.type === "success"
        ) {
          this.dialog = false;
          this.$refs.form.reset();
        }
      }
      if (this.setup_dialog) {
        if (
          this.getServerMessage.active &&
          this.getServerMessage.type === "success"
        ) {
          this.setup_dialog = false;
          this.$refs.setup_form.reset();
        }
      }
    }
  }
};
</script>
