import axios from "axios";

const state = {
    ing_type: [],
}

const getters = {
    getRequisiteTypes(state) {
        return state.ing_type
    }
}

const mutations = {
    setRequisiteTypes(state, data){
        state.ing_type = data
    },
}

const actions = {
    getRequisitesTypesList({commit, rootState}){
        axios.get("/requisite-types", rootState.authentication.accessTokenConfig)
        .then(res => {
            commit('setRequisiteTypes', res.data)
        })
        .catch(error => {
            console.log(error.response);
        })
    },
    setRequisiteType({ commit, dispatch, rootState }, data) {
  
        axios.post("/requisite-types/add", data, rootState.authentication.accessFileTokenConfig )
        .then(res => {
            if(res.status == 201){
                commit('setServerMessage', { message:res.data.message, heading:'New requisite type added', active:true, type:'success' }, { root: true })
                dispatch('getRequisitesTypesList')
            }
        })
        .catch(error => {
            commit('setServerMessage', { message:error.response.data.message, heading:'New requisite type adding failed!', active:true, type:'error' }, { root: true })
            console.log(error.response);
        })
      },
}

export default {
    state,
    mutations,
    actions,
    getters
}