<template>
  <div class="Devices conteiner" style="width:inherit">
    <div class="heading d-flex">
      <div>
        <v-card
          color="accent"
          height="65px"
          width="65px"
          class="d-flex justify-center align-center"
        >
          <img height="35px" width="35px" src="@/assets/icons/device-white.svg" />
        </v-card>
      </div>
      <div style="width:100%;margin-left:10px">
        <v-card height="65px">
          <v-card-title>Overview</v-card-title>
        </v-card>
      </div>
    </div>
    <div class="content">
      <div class="d-flex mt-3 mb-3">
        <v-card
          :class="getClass(item)"
          style="overflow:hidden"
          cols="3"
          v-for="item in widgets"
          :key="item.name"
        >
          <v-row no-gutters style="border-bottom:1px solid rgba(0,0,0,.1)">
            <v-col cols="8">
              <v-card-title>{{item.name}}</v-card-title>
              <v-card-text class="display-1 number">{{item.count}}</v-card-text>
            </v-col>
            <v-col cols="4">
              <v-img src="@/assets/image_placeholder.png"></v-img>
            </v-col>
          </v-row>
          <v-row no-gutters>
            <v-col>
              <p class="font-weight-regular caption text-center" style="margin:0px; padding:10px">
                <v-chip
                  :color="getColor(item.last7)"
                  text-color="white"
                  class="ma-2 font-weight-light"
                  small
                >{{item.last7}}%</v-chip>in last 7 days
                <v-chip
                  text-color="white"
                  :color="getColor(item.last30)"
                  class="ma-2 font-weight-light"
                  small
                >{{item.last30}}%</v-chip>in last 30 days
              </p>
            </v-col>
          </v-row>
        </v-card>
      </div>
      <div  class="d-flex">
        <v-row>
          <v-col cols="8">
            <v-card>
              <v-card-title>Dispenses</v-card-title>
              <apexchart type="area" height="335" width="1000" :options="chartOptions" :series="series"></apexchart>
            </v-card>
          </v-col>
          <v-col cols="4">
            <v-card>
              <v-card-title>Dispense-o-meter</v-card-title>
              <apexchart type="pie"  height="350" width="480" :options="dispenseOptions" :series="dispensemeter"></apexchart>
            </v-card>
          </v-col>
        </v-row>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Overview",
  data() {
    return {
      widgets: [
        { name: "Total Devices", count: "02", last7: -10, last30: +10 },
        { name: "Active Devices", count: "02", last7: -10, last30: +10 },
        { name: "Alcohol Dispenses", count: "110", last7: -10, last30: +10 },
        { name: "Cocktails Dispenses", count: "150", last7: 0, last30: +10 }
      ],
      series: [
        {
          name: "Alcohols",
          data: [31, 40, 28, 51, 42, 109, 100]
        },
        {
          name: "Cocktails",
          data: [11, 32, 45, 32, 34, 52, 41]
        }
      ],
      chartOptions: {
        chart: {
          height: 350,
          type: "area"
        },
        dataLabels: {
          enabled: false
        },
        stroke: {
          curve: "smooth"
        },
        xaxis: {
          type: "datetime",
          categories: [
            "2018-09-19T00:00:00.000Z",
            "2018-09-19T01:30:00.000Z",
            "2018-09-19T02:30:00.000Z",
            "2018-09-19T03:30:00.000Z",
            "2018-09-19T04:30:00.000Z",
            "2018-09-19T05:30:00.000Z",
            "2018-09-19T06:30:00.000Z"
          ]
        },
        tooltip: {
          x: {
            format: "dd/MM/yy HH:mm"
          }
        }
      },
      dispensemeter: [440, 550],
      dispenseOptions: {
        chart: {
          width: 200,
          type: "pie"
        },
        labels: ["Alcohols", "Cocktails"],
        responsive: [
          {
            breakpoint: 200,
            options: {
              chart: {
                width: 200
              },
              legend: {
                position: "bottom"
              }
            }
          }
        ]
      }
    };
  },
  methods: {
    getColor(value) {
      if (value < 0) return "red";
      if (value > 0) return "green";
      if (value == 0) return "orange";
    },
    getClass(item) {
      if (this.widgets.lastIndexOf(item) == this.widgets.length - 1)
        return "mr-md-0";
      else return "mr-md-3 mr-lg-3";
    }
  }
};
</script>

<style scoped>
.display-1.number {
  font-family: Roboto !important;
}
.container {
  /* max-width: 1140px; */
}
</style>
