<template>
  <div>
    <v-navigation-drawer v-model="drawer" app clipped width="300px">
      <v-list-item two-line>
        <v-list-item-avatar>
          <img src="@/assets/logo.png" />
        </v-list-item-avatar>
        <v-list-item-content>
          <v-list-item-title>{{ User.name }}</v-list-item-title>
          <v-list-item-subtitle>{{ User.email }}</v-list-item-subtitle>
        </v-list-item-content>
      </v-list-item>
      <v-divider></v-divider>

      <v-list dense nav light>
        <v-list-item-group color="primary">
          <template v-for="item in items">
            <v-list-item v-if="!item.items" light :key="item.title" :to="item.route" exact>
              <v-list-item-icon>
                <v-icon>{{ item.icon }}</v-icon>
              </v-list-item-icon>

              <v-list-item-content>
                <v-list-item-title>{{ item.title }}</v-list-item-title>
              </v-list-item-content>
            </v-list-item>
            <v-list-group v-if="item.items"
              :key="item.title"
              :prepend-icon="item.icon"
              no-action
            >
              <template v-slot:activator>
                <v-list-item-content>
                  <v-list-item-title v-text="item.title"></v-list-item-title>
                </v-list-item-content>
              </template>

              <v-list-item v-for="subItem in item.items" :key="subItem.title" :to="subItem.route" exact>
                <v-list-item-content>
                  <v-list-item-title v-text="subItem.title"></v-list-item-title>
                </v-list-item-content>
              </v-list-item>
            </v-list-group>
          </template>
        </v-list-item-group>
      </v-list>
    </v-navigation-drawer>

    <v-app-bar app clipped-left>
      <v-app-bar-nav-icon @click.stop="drawer = !drawer" />
      <div style="border-radius:50%; height:50px;width:50px;margin-right:10px">
        <img style="height:50px;width:50px" src="@/assets/logo.png" />
      </div>
      <v-toolbar-title>Administrator Console</v-toolbar-title>
    </v-app-bar>

    <v-content>
      <v-container class="fill-height" fluid>
        <router-view />
      </v-container>
    </v-content>

    <v-footer app>
      <span>&copy; 2019</span>
    </v-footer>
    <v-row justify="center">
      <v-dialog v-model="logoutDialog" persistent max-width="350">
        <v-card>
          <v-card-title
            class="headline center"
            style="justify-content:center"
          >User Session Timeout</v-card-title>
          <v-card-text class="center">
            User is inactive for longtime. Automatically logged out in
            <span
              style="color:red!important"
            >{{countDown}}</span> seconds. Do you want to stay logged?
          </v-card-text>
          <v-card-actions style="justify-content:center">
            <v-btn text @click="logout">Logout</v-btn>
            <v-btn text @click="stay">Stay in</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
      <v-dialog v-model="getServerMessage.active" persistent max-width="350">
        <v-card>
          <v-card-title
            class="center"
            style="justify-content:center"
          >{{ getServerMessage.heading }}</v-card-title>
          <v-card-text color="red" class="text-center body-1">{{ getServerMessage.message }}</v-card-text>
          <v-card-actions style="justify-content:center">
            <v-btn text @click="closeDialog">close</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-row>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import router from "../router";
export default {
  name: "Admin",
  components: {
    // Home
  },

  data() {
    return {
      drawer: true,
      selected: "",
      items: [
        { title: "Overview", icon: "mdi-view-dashboard", route: "/admin" },
        {
          title: "Analytics",
          icon: "insert_chart_outlined",
          items: [
            { title: "Devices Analytics", route: "/admin/analytics/devices" },
            { title: "Dispense Analytics", route: "/admin/analytics/dispenses" },
          ]
        },
        { 
          title: "Devices Catalogue", 
          icon: "devices_other", 
          items: [
            { title: "Devices", route: "/admin/devices" },
            { title: "Bartiny", route: "/admin/devices/bartiny" }
          ] 
        },
        { 
          title: "Products Catalogue", 
          icon: "category", 
          route: "/admin/products"
        },
        {
          title: "Requisites Library",
          icon: "mdi-cupboard-outline",
          items: [
            { title: "Requisites", route: "/admin/requisites" },
            { title: "Requisite Types", route: "/admin/requisites/types" },
            { title: "Requisite Groups", route: "/admin/requisites/groups" }
          ]
        },
        {
          title: "Cocktails Library",
          icon: "mdi-glass-wine",
          items: [
            { title: "Cocktails", route: "/admin/cocktails" },
            { title: "Cocktail Types", route: "/admin/cocktails/types" },
          ]
        },
        { title: "Users Management", 
          icon: "supervisor_account",
          items: [
            { title: "Users", route: "/admin/users" },
            { title: "Super Users", route: "/admin/superusers" },
          ]
          },
        {
          title: "Support Management",
          icon: "help_outline",
          route: "/admin/Support"
        },
        { title: "Settings", icon: "settings", route: "/admin/settings" },
        { title: "Logout", icon: "lock", route: "/auth/logout" }
      ],
      right: false,
      miniVariant: false,
      expandOnHover: false,
      background: false,
      countDown: 30,
      pageActive: true,
      counter: 0,
      timer1: null,
      pageActiveTimer: null
    };
  },
  created() {
    if (this.isAuthenticated) {
      this.$store.dispatch("tryAutoLogin");
    }
  },
  watch: {
    logoutDialog: function() {
      if (this.logoutDialog) {
        clearInterval(this.timer1);
        this.countDown = 30;
        this.countDownTimer();
      }
    }
  },
  methods: {
    logout() {
      this.$store.dispatch("closeLogoutDialog");
      router.replace("/auth/logout");
      clearInterval(this.timer1);
    },
     closeDialog() {
      this.$store.dispatch("clearMessage");
    },
    stay() {
      this.$store.dispatch("closeLogoutDialog");
      this.$store.dispatch("reLogin");
      clearInterval(this.timer1);
    },
    countDownTimer() {
      this.timer1 = setInterval(() => {
        if (this.countDown > 0) {
          this.countDown--;
        } else {
          this.logout();
        }
      }, 1000);
    },
    checkPageActive() {
      this.pageActiveTimer = setInterval(() => {
        this.counter++;
        console.log(this.counter);
        if (this.counter > 890) {
          this.pageActive = false;
        }
      }, 1000);
    }
  },
  computed: {
    ...mapGetters(["User", "logoutDialog", "isAuthenticated","getServerMessage"])
  }
};
</script>
