<template>
  <div class="ing_types" style="width:inherit">
    <div class="heading d-flex">
      <div>
        <v-card
          color="accent"
          height="65px"
          width="65px"
          class="d-flex justify-center align-center" >
          <img height="35px" width="35px" src="@/assets/icons/ingredients-white.svg" />
        </v-card>
      </div>
      <div style="width:100%;margin-left:10px">
        <v-card height="65px">
          <v-card-title>Super Users Management</v-card-title>
        </v-card>
      </div>
    </div>
    <div class="content">
      <v-card class="mt-3 pa-3 pb-0">
        <v-row class="pb-0">
          <v-col class="pb-0">
            <v-text-field dense label="Search"  v-model="search" single-line outlined append-icon="search"></v-text-field>
          </v-col>
          <v-col class="pb-0">
          </v-col>
          <v-col class="pb-0">
          </v-col>
          <v-col class="pb-0">
          </v-col>
          <v-col class="pb-0">
          </v-col>
        </v-row>
      </v-card>
      <v-card style="margin-top:10px">
        <v-card-title>
          <v-spacer></v-spacer>
          <v-btn depressed color="primary" @click="addNewDialog()">Add New</v-btn>
        </v-card-title>
        <v-data-table :headers="headers" :search="search" :items="getSuperUsers" :items-per-page="10">
          <!-- <template v-slot:item.image="{ item }">
            <div class="p-2">
              <v-img :src="item.img_uri" :alt="item.name" height="40px" width="40px" style="border-radius:20px;"></v-img>
            </div>
          </template> -->
          <!-- <template v-slot:item.roles="{ item }">
            <v-chip-group column>
              <v-chip v-for="ite in item.roles" :key="ite">{{ ite }}</v-chip>
            </v-chip-group>
          </template> -->
        </v-data-table>
      </v-card>
    
    </div>
     <div>
        <v-dialog v-model="new_dialog" persistent max-width="400px">
          <v-card>
            <v-container>
              <v-card-title>
                <span class="headline">New Requisite</span>
              </v-card-title>
              <v-card-text>
                <v-form ref="new_form" v-model="valid" lazy-validation>
                  <v-row no-gutters>
                    <v-col cols="12">
                      <v-text-field
                        outlined
                        dense
                        label="First Name*"
                        :rules="new_rules.first_name"
                        v-model="new_form.first_name"
                        required
                      ></v-text-field>
                    </v-col>
                    <v-col cols="12">
                      <v-text-field
                        outlined
                        dense
                        label="Last Name*"
                        :rules="new_rules.last_name"
                        v-model="new_form.last_name"
                        required
                      ></v-text-field>
                    </v-col>
                    <v-col cols="12">
                      <v-text-field
                        outlined
                        dense
                        label="Email ID*"
                        :rules="new_rules.email"
                        v-model="new_form.email"
                        required
                      ></v-text-field>
                    </v-col>
                    <v-col cols="12">
                      <v-text-field
                        outlined
                        dense
                        label="Mobile Number*"
                        :rules="new_rules.mobile"
                        v-model="new_form.mobile"
                        required
                      ></v-text-field>
                    </v-col>
                    <v-col cols="12">
                      <v-text-field
                        outlined
                        dense
                        label="Password*"
                        :rules="new_rules.password"
                        v-model="new_form.password"
                        required
                        type="password"
                      ></v-text-field>
                    </v-col>
                  </v-row>
                </v-form>
                <small>*indicates required field</small>
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="secondary" @click="new_dialog = false">Cancel</v-btn>
                <v-btn color="primary" @click="newIngredient()">Add</v-btn>
              </v-card-actions>
            </v-container>
          </v-card>
        </v-dialog>
      </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "SuperUsers",
  data() {
    return {
      show: false,
      valid: true,
      new_dialog: false,
      search:'',
      headers: [
        { text: "First Name", value: "first_name" },
        { text: "Last Name", value: "last_name" },
        { text: "Email", value: "email" },
        { text: "Mobile", value: "mobile" },
        { text: "Active", value: "is_active" },
        { text: "Created at", value: "created_at" },
        { text: "Last Logged", value: "last_logged" },
        // { text: "Roles", value: "roles" }
      ],
      new_form:{
        email:'',
        first_name:'',
        last_name:'',
        mobile:'',
        password:''
      },
      new_rules:{
        email: [v => !!v || "E-mail is required",
            v => /.+@.+\..+/.test(v) || "E-mail must be valid"],
        first_name: [value => !!value || "First name required."],
        last_name: [value => !!value || "Last name required."],
        mobile: [value => !!value || "Mobile number required."],
        password: [value => !!value || "Password required."],
      }
    };
  },
   created () {
     if (this.getSuperUsers.length == 0) {
      this.$store.dispatch('getSuperUsersList')
    }
  },
  computed: {
    ...mapGetters([
      "getSuperUsers",
      "getServerMessage"
    ]),
  },
  methods:{
     addNewDialog() {
      this.new_dialog = true;
    },
    newIngredient() {
      if (this.$refs.new_form.validate()) {
        this.$store.dispatch("setSuperUser", this.new_form);
      }
    },
  },
  watch:{
     getServerMessage: function() {
      if (this.new_dialog) {
        if (
          this.getServerMessage.active &&
          this.getServerMessage.type === "success"
        ) {
          this.new_dialog = false;
          this.$refs.new_form.reset();
        }
      }
    }
  }
};
</script>
