<template>
  <div class="requisites" style="width:inherit">
    <div class="heading d-flex">
      <div>
        <v-card
          color="accent"
          height="65px"
          width="65px"
          class="d-flex justify-center align-center"
        >
          <img height="35px" width="35px" src="@/assets/icons/ingredients-white.svg" />
        </v-card>
      </div>
      <div style="width:100%;margin-left:10px">
        <v-card height="65px">
          <v-card-title>Requisites</v-card-title>
        </v-card>
      </div>
      <div style="margin-left:10px">
        <v-card
          color="accent"
          height="65px"
          width="65px"
          class="d-flex justify-center align-center"
          @click="toggleView"
        >
          <v-icon color="white">{{ list? 'format_list_bulleted':'apps' }}</v-icon>
        </v-card>
      </div>
    </div>
    <div class="content">
      <v-card class="mt-3 pa-3 pb-0">
        <v-row>
          <v-col class="pb-0">
            <v-text-field
              dense
              label="Search Requisites"
              v-model="search"
              outlined
              append-icon="search"
            ></v-text-field>
          </v-col>
          <v-col class="pb-0">
            <v-select
              dense
              :items="generic_type"
              item-text="group_name"
              item-value="id"
              label="Select Requisite Group "
              outlined
              v-model="selectedGroup"
              @change="filterIngTypes()"
            ></v-select>
          </v-col>
          <v-col class="pb-0">
            <v-select
              dense
              :items="filteredIngTypes"
              item-text="type_name"
              item-value="id"
              label="Select Requisites Type"
              outlined
              v-model="selectedType"
              @change="filterIng()"
            ></v-select>
          </v-col>
          <v-col class="pb-0"></v-col>
          <v-col class="pb-0"></v-col>
        </v-row>
      </v-card>
      <v-card v-if="list" style="margin-top:10px">
        <v-card-title>
          <v-spacer></v-spacer>
          <v-btn depressed color="primary" @click="addNewRequisiteDialog()">Add New</v-btn>
        </v-card-title>
        <v-data-table
          :headers="headers"
          :search="search"
          :items="filteredRequisites"
          :items-per-page="10"
          :sort-by="['Requisite Type', 'Requisite Name']"
          :sort-desc="[true, true]"
        >
          <template v-slot:item.imageURI="{ item }">
            <div class="p-2">
              <v-img
                :src="item.img_uri"
                :alt="item.ing_name"
                height="40px"
                width="40px"
                style="border-radius:20px; border:1px solid rgb(0,0,0,.1)"
              ></v-img>
            </div>
          </template>
          <template v-slot:item.quantity="{ item }">
            <v-chip-group column>
              <v-chip v-for="ite in item.quantity" :key="ite">{{ ite }}{{item.quant_units}}</v-chip>
            </v-chip-group>
          </template>
        </v-data-table>
      </v-card>
      <div v-else style="margin-top:10px">
        <v-data-iterator :items="filteredRequisites" :items-per-page.sync="itemsPerPage" :page="page" :search="search" hide-default-footer>
          <template v-slot:default="props">
            <v-row>
              <v-col v-for="item in props.items" :key="item.name" cols="12" sm="6" md="4" lg="3">
                <v-card>
                  <v-card>
                    <v-img :src="item.img_uri" height="250px" style="background:rgb(0,0,0,.1)"></v-img>
                    <v-card-title class="subheading font-weight-bold">{{ item.ing_name }}</v-card-title>
                    <v-card-subtitle>{{item.ing_type_name}}</v-card-subtitle>
                    <v-list dense>
                      <v-list-item>
                        <v-list-item-content>Generic Group:</v-list-item-content>
                        <v-list-item-content class="align-end">{{ item.gen_type }}</v-list-item-content>
                      </v-list-item>

                      <v-list-item>
                        <v-list-item-content>Machine Dispensable:</v-list-item-content>
                        <v-list-item-content class="align-end">{{ item.dispensable }}</v-list-item-content>
                      </v-list-item>

                      <v-list-item>
                        <v-list-item-content>Quantity</v-list-item-content>
                        <v-list-item-content></v-list-item-content>
                      </v-list-item>
                      <v-list-item>
                        <v-chip-group column active-class="primary--text">
                          <v-chip
                            v-for="ite in item.quantity"
                            :key="ite"
                          >{{ ite }}{{item.quant_units}}</v-chip>
                        </v-chip-group>
                      </v-list-item>
                    </v-list>
                    <v-divider></v-divider>
                    <v-card-actions>
                      <v-spacer></v-spacer>
                      <v-btn text>Edit</v-btn>
                    </v-card-actions>
                  </v-card>
                </v-card>
              </v-col>
            </v-row>
          </template>
        </v-data-iterator>
        <template>
          <div class="text-center">
            <v-pagination
              v-model="page"
              :length="numberOfPages"
              prev-icon="mdi-menu-left"
              next-icon="mdi-menu-right"
              @next="next()"
            ></v-pagination>
          </div>
        </template>
      </div>
      <div>
        <v-dialog v-model="new_dialog" persistent max-width="400px">
          <v-card>
            <v-container>
              <v-card-title>
                <span class="headline">New Requisite</span>
              </v-card-title>
              <v-card-text>
                <v-form ref="new_form" v-model="valid" lazy-validation>
                  <v-row no-gutters>
                    <v-col>
                      <v-card
                        id="preview"
                        height="200px"
                        width="200px"
                        class="mx-auto mt-2 mb-6 pa-4"
                        outlined
                        @click.native="openFileDialog"
                      >
                        <v-img width="100%" height="100%" :src="getImgUrl(url)" />
                      </v-card>
                      <div v-if="image_error" class="v-messages__message mb-3" style="text-align:center; color:red">Image required.</div>
                    </v-col>
                    <v-col cols="12">
                      <v-file-input
                        style="display:none"
                        id="file"
                        required
                        accept="image/png, image/jpeg, image/bmp"
                        placeholder="Pick an image"
                        prepend-icon="mdi-camera"
                        label="Requisite Image"
                        outlined
                        :rules="new_rules.image"
                        @change="onFileChange"
                      ></v-file-input>
                    </v-col>
                    <v-col cols="12">
                      <v-text-field
                        outlined
                        dense
                        label="Requisite Name*"
                        :rules="new_rules.name"
                        v-model="new_form.name"
                        required
                      ></v-text-field>
                    </v-col>
                    <v-col cols="12">
                      <v-select
                        outlined
                        :items="getRequisiteTypes"
                        item-text="name"
                        item-value="id"
                        dense
                        label="Requisite Type*"
                        :rules="new_rules.type_id"
                        v-model="new_form.type_id"
                        required
                      ></v-select>
                    </v-col>
                    <v-col cols="12">
                      <v-combobox
                        outlined
                        v-model="new_form.quantity"
                        :items="default_volumes"
                        chips
                        :rules="new_rules.quantity"
                        clearable
                        label="Quantity Offered"
                        multiple
                        required
                      >
                        <template v-slot:selection="{ attrs, item, select, selected }">
                          <v-chip
                            v-bind="attrs"
                            :input-value="selected"
                            close
                            @click="select"
                            @click:close="remove(item)"
                          >
                            <strong>{{ item }}</strong>&nbsp;
                            <span>{{ new_form.quant_units }}</span>
                          </v-chip>
                        </template>
                      </v-combobox>
                    </v-col>
                  </v-row>
                </v-form>
                <small>*indicates required field</small>
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="secondary" @click="new_dialog = false">Cancel</v-btn>
                <v-btn color="primary" @click="newRequisite()">Add</v-btn>
              </v-card-actions>
            </v-container>
          </v-card>
        </v-dialog>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  name: "Requisites",
  data() {
    return {
      show: false,
      list: true,
      page: 1,
      valid: true,
      image_error:false,
      new_dialog: false,
      itemsPerPage: 12,
      selectedType: "",
      selectedGroup: "",
      search: "",
      url:null,
      file:null,
      headers: [
        { text: "Image", value: "imageURI", sortable: false },
        {
          text: "Requisite Name",
          align: "start",
          value: "name",
          filterable: true
        },
        { text: "Requisite Type", value: "type_name" },
        // { text: "Requisite Group", value: "gen_type" },
        { text: "Machine Dispensable", value: "dispensable" },
        { text: "Quantity", value: "quantity" }
      ],
      filteredRequisites: this.requisites,
      filteredIngTypes: [],
      new_form: {
        name: "",
        type_id: "",
        quant_units: "",
        quantity: "",
        image: ""
      },
      new_rules: {
        name: [value => !!value || "Requisite name required."],
        type_id: [value => !!value || "Requisite type required."],
        quant_units: [value => !!value || "Measuring Units required."],
        quantity: [v => (v && v.length > 0) || "Quantity Offered Required"],
        image: [
          v => !!v || "Image is required",
          value =>
            !value || value.size < 1000000 || "Image should be less than 2 MB!"
        ]
      },
      default_volumes: [2000, 1000, 750, 700, 500, 300]
    };
  },
  methods: {
    toggleView() {
      this.list = !this.list;
    },
    nextPage() {
      if (this.page + 1 <= this.numberOfPages) this.page += 1;
    },
    formerPage() {
      if (this.page - 1 >= 1) this.page -= 1;
    },
    updateItemsPerPage(number) {
      this.itemsPerPage = number;
    },
    filterIngTypes() {
      this.filteredIngTypes = [];
      this.filteredIngTypes = this.ing_types.filter(type => {
        if (
          type.group_id == this.selectedGroup ||
          this.selectedGroup == "All" ||
          this.selectedGroup === ""
        ) {
          return type;
        }
      });
    },
    filterIng() {
      this.filteredRequisites = [];
      this.filteredRequisites = this.requisites.filter(ing => {
        if (
          ing.type_id == this.selectedType ||
          this.selectedType == "All" ||
          this.selectedType == ""
        ) {
          return ing;
        }
      });
    },
    addNewRequisiteDialog() {
      this.new_dialog = true;
    },
    newRequisite() {
      if (this.$refs.new_form.validate()) {
        let formData = new FormData();
        formData.append('image',this.file)
        formData.append('ing_name',this.new_form.ing_name)
        formData.append('ing_type_id',this.new_form.ing_type_id)
        formData.append('quantity',this.new_form.quantity)
        console.log(this.new_form.quantity)
        this.$store.dispatch("setRequisite", formData);
      } else {
        if(!this.file){
          this.image_error=true
        }
      }
    },
    openFileDialog() {
      document.getElementById("file").click();
    },
    onFileChange(e) {
      this.file = e;
      this.url = URL.createObjectURL(e);
      console.log(e)
    },
    getImgUrl(url) {
      if (url == null) {
        return require("@/assets/image_placeholder.png");
      } else {
        return url;
      }
    },
    remove(item) {
      this.new_form.quantity.splice(this.new_form.quantity.indexOf(item), 1);
      this.new_form.quantity = [...this.new_form.quantity];
    }
  },
  computed: {
    ...mapGetters(["getRequisites", "getRequisiteGroups", "getRequisiteTypes","getServerMessage"]),
    requisites() {
      return this.getRequisites;
    },
    generic_type() {
      const SelectValue = ["All"];
      return SelectValue.concat(this.getRequisiteGroups);
    },
    ing_types() {
      const SelectValue = ["All"];
      return SelectValue.concat(this.getRequisiteTypes);
    },
    numberOfPages() {
      return Math.ceil(this.requisites.length / this.itemsPerPage);
    }
  },
  created() {
    if (this.getRequisites.length == 0) {
      this.$store.dispatch("getRequisitesList");
    }
    if (this.getRequisiteTypes.length == 0) {
      this.$store.dispatch("getRequisitesTypesList");
    }
    if (this.getRequisiteGroups.length == 0) {
      this.$store.dispatch("getRequisiteGroupsList");
    }

    this.filterIngTypes();
    this.filterIng();
  },
  watch: {
    getRequisites: function() {
      this.filteredRequisites = this.requisites;
    },
    getServerMessage: function() {
      if (this.new_dialog) {
        if (
          this.getServerMessage.active &&
          this.getServerMessage.type === "success"
        ) {
          this.new_dialog = false;
          this.$refs.new_form.reset();
        }
      }
    }
  },
};
</script>

<style>
.v-image__image {
  background-size: contain;
}
</style>
<style scoped>
#preview {
  display: flex;
  justify-content: center;
  align-items: center;
}

#preview img {
  max-width: 100%;
  max-height: 500px;
}
</style>




