<template>
  <div>
    <div class="intro-section" :style="{ backgroundImage: `url(${backgroundUrl})` }">
      <v-container class="intro-container">
        <v-row class="intro-content-box" style="margin-top:40px">
          <v-col class="col-12 d-flex">
            <v-row>
              <v-col class="col-12">
                <h1 class="tagline">Reach us</h1>
              </v-col>
              <v-col class="col-12">
                <h5 class="info-text">Contact us by submitting your query through following form</h5>
              </v-col>
            </v-row>
          </v-col>
          <!-- <v-col class="col-12 col-md-6 d-flex justify-center">
            <div style="max-width:700px">
              <v-img height="200px" src="@/assets/logo-cap.svg"></v-img>
            
            </div>
          </v-col>-->
        </v-row>
      </v-container>
    </div>
    <div>
      <v-container>
        <v-card class="mx-auto">
          <v-row no-gutters>
            <v-col>
              <iframe
                src="https://www.google.com/maps/embed?pb=!1m14!1m8!1m3!1d15225.97866143104!2d78.4408975!3d17.4360225!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x0%3A0x21deed69d284fe86!2sAiila%20Innovations!5e0!3m2!1sen!2sin!4v1592635421429!5m2!1sen!2sin"
                width="100%"
                height="450"
                frameborder="0"
                style="border:0;"
                allowfullscreen
                aria-hidden="false"
                tabindex="0"
              ></iframe>
            </v-col>
          </v-row>
          <v-row>
            <v-col cols="12" md="8">
              <v-form
                style="max-width:600px;margin:auto"
                ref="form"
                v-model="valid"
                lazy-validation
              >
                <div style="padding:30px;width:100%">
                  <div class="center">
                    <p class="title font-weight-reular">Please fill the following form to get quick support</p>
                  </div>
                  <v-row>
                    <v-col>
                      <v-row no-gutters>
                        <v-col cols="12" md="6" lg="6">
                          <v-text-field
                            dense
                            outlined
                            label="First Name"
                            prepend-inner-icon="person"
                            v-model="form.first_name"
                            :rules="firstNameRules"
                            required
                          ></v-text-field>
                        </v-col>
                        <v-col cols="12" md="6" lg="6" class="gutter">
                          <v-text-field
                            dense
                            outlined
                            label="Last Name"
                            v-model="form.last_name"
                            :rules="lastNameRules"
                            required
                          ></v-text-field>
                        </v-col>
                      </v-row>
                      <v-text-field
                        dense
                        outlined
                        label="Email"
                        prepend-inner-icon="email"
                        :rules="emailRules"
                        required
                        v-model="form.email"
                      ></v-text-field>
                      <v-text-field
                        dense
                        outlined
                        label="Mobile Number"
                        prepend-inner-icon="phone"
                        :rules="mobileRules"
                        v-model="form.mobile"
                      ></v-text-field>
                      <v-text-field
                        dense
                        outlined
                        label="Company/Organisation"
                        prepend-inner-icon="mdi-bank"
                        :rules="orgRules"
                        required
                        v-model="form.organization"
                      ></v-text-field>
                    </v-col>
                  </v-row>
                <v-row>
                    <v-col>
                        <v-textarea prepend-inner-icon="mdi-message" outlined dense v-model="form.messages" label="Message*" required :rules="messageRules"></v-textarea>
                    </v-col>
                </v-row>
                  <v-row align="center">
                    <v-col class="text-center" cols="12">
                      <v-btn color="primary" @click="validate">Submit</v-btn>
                    </v-col>
                  </v-row>
                  <!-- <v-row>
                            <v-col>
                            <v-alert dismissible :type="authMessage.type" v-if="authMessage.msg != null">{{ authMessage.msg }}</v-alert>
                            </v-col>
                  </v-row>-->
                  <v-row align="center" no-gutters>
                    <v-col class="text-center" cols="12">
                      <p style="font-size:12px; margin-bottom:0px">
                        By clicking submit you agree to
                        <router-link
                          class="link"
                          small
                          text
                          to="/auth/terms"
                          style="text-transform:lowercase"
                        >terms of use</router-link>
                      </p>
                    </v-col>
                  </v-row>
                  <v-row justify="center">
                    <v-dialog v-model="dialog" persistent max-width="350">
                      <v-card>
                        <v-card-title
                          class="headline center"
                          style="justify-content:center"
                        >{{ authMessage.heading }}</v-card-title>
                        <v-card-text class="center">{{ authMessage.msg }}</v-card-text>
                        <v-card-actions style="justify-content:center">
                          <v-btn text @click="closeDialog">close</v-btn>
                        </v-card-actions>
                      </v-card>
                    </v-dialog>
                  </v-row>
                </div>
              </v-form>
            </v-col>
            <v-col cols="12" md="4" style="border-left:1px solid rgb(0,0,0,.1)">
              <div style="padding:30px" class="line-top">
                <div style="display:flex">
                  <div style="padding-top:10px">
                    <div
                      style=" border-radius:30px; border: 1px solid #2979ff ; color:#2979ff ; height:30px;width:30px; margin-right: 15px;"
                    >
                      <i style="font-size:14px;padding: 7.5px;" class="material-icons">location_on</i>
                    </div>
                  </div>
                  <div>
                    <p class="para">
                      <strong>Address:</strong>
                      <br />202, Swarna Latha Estates, Yousufguda, Hyderabad, Telangana, India-500073
                    </p>
                  </div>
                </div>
                <div style="display:flex">
                  <div style="padding-top:10px">
                    <div
                      style=" border-radius:30px; border: 1px solid #2979ff ; color:#2979ff ; height:30px;width:30px; margin-right: 15px;"
                    >
                      <i style="font-size:14px;padding: 7.5px;" class="material-icons">phone</i>
                    </div>
                  </div>
                  <div>
                    <p class="para">
                      <strong>Phone Number:</strong>
                      <br />+9140 4853-3841
                    </p>
                  </div>
                </div>
                <div style="display:flex">
                  <div style="padding-top:10px">
                    <div
                      style=" border-radius:30px; border: 1px solid #2979ff ; color:#2979ff ; height:30px;width:30px; margin-right: 15px;"
                    >
                      <i style="font-size:14px;padding: 7.5px;" class="material-icons">print</i>
                    </div>
                  </div>
                  <div>
                    <p class="para">
                      <strong>Fax Number:</strong>
                      <br />+9140 4853-3842
                    </p>
                  </div>
                </div>
                <div style="display:flex">
                  <div style="padding-top:10px">
                    <div
                      style=" border-radius:30px; border: 1px solid #2979ff ; color:#2979ff ; height:30px;width:30px; margin-right: 15px;"
                    >
                      <i style="font-size:14px;padding: 7.5px;" class="material-icons">mail</i>
                    </div>
                  </div>
                  <div>
                    <p class="para">
                      <strong>Email:</strong>
                      <br />info@aiila.com
                    </p>
                  </div>
                </div>
                <div style="display:flex">
                  <div style="padding-top:10px">
                    <div
                      style=" border-radius:30px; border: 1px solid #2979ff ; color:#2979ff ; height:30px;width:30px; margin-right: 15px;"
                    >
                      <i style="font-size:14px;padding: 7.5px;" class="material-icons">access_time</i>
                    </div>
                  </div>
                  <div>
                    <p class="para">
                      <strong>Office Hours:</strong>
                      <br />Monday - Friday:- 9:00 AM to 6:00 PM
                      <br />Sunday: Closed
                    </p>
                  </div>
                </div>
              </div>
            </v-col>
          </v-row>
        </v-card>
      </v-container>
    </div>
  </div>
</template>
<script>
import backgroundUrl from "@/assets/bgc.png";
import { mapGetters } from "vuex";
export default {
  data() {
    return {
      backgroundUrl,
      dialog: false,
      form: {
        first_name: "",
        last_name: "",
        email: "",
        mobile: "",
        organisation: "",
        message:""
      },
      orgRules: [
        v => !!v || "Organization/Company is required"
      ],
      messageRules: [
        v => !!v || "Message is required"
      ],
      emailRules: [
        v => !!v || "E-mail is required",
        v => /.+@.+\..+/.test(v) || "E-mail must be valid"
      ],
      firstNameRules: [
        v => !!v || "First name is required",
        v => (v && v.length >= 2) || "First Name must have 2+ characters"
      ],
      lastNameRules: [
        v => !!v || "Last name is required",
        v => (v && v.length >= 2) || "Last Name must have 2+ characters"
      ],
      mobileRules: [
        v => !!v || "Mobile Number is required",
        v =>
          /^[a-zA-Z0-9\-().\s]{10,15}$/.test(v) || "Mobile number must be valid"
      ]
    };
  },
  methods: {
    validate() {
      if (this.$refs.form.validate()) {
        console.log(this.form);
        this.$store.dispatch("register", this.form);
      }
    },
    closeDialog() {
      if (this.authMessage.type == "success") this.$refs.form.reset();
      this.$store.dispatch("resetMessage");
    }
  },
  computed: {
    ...mapGetters(["authMessage"])
  }
};
</script>

<style scoped>
.v-sheet {
  border-radius: 0px;
  background-color: unset;
}
.intro-container {
  padding-top: 64px;
  width: 100%;
}

.intro-content-box {
  height: 400px;
}

.intro-section {
  backdrop-filter: blur(8px);
  width: auto;
  background-size: cover;
  margin: 0;
  height: 500px;
  width: 100%;
}
.tagline {
  font-weight: 900;
  font-size: 70px;
  color: white;
}
.info-text {
  color: rgb(255, 255, 255, 0.7);
  font-family: Nunito;
}

.heading_style {
  background-color: white;
  padding: 16px;
}
.v-card img {
  margin: 10px;
}

.center{
    text-align: center;
}

.link{
    text-transform: Capitalize;
    text-decoration: none;
    font-weight: 400;
    font-size: 12px;
}
@media (min-width: 960px)  {
  
  .gutter{
    padding-left: 10px;
  }
}
</style>