import axios from "axios";

const state = {
    generic_type: []
}

const getters = {
    getRequisiteGroups(state) {
        return state.generic_type
    },
}

const mutations = {
    newRequisiteGroup (state,payload) {
        state.generic_type.push(payload);
    },
    setRequisiteGroups(state, data){
        state.generic_type = data
    },
}

const actions = {
    newRequisiteGroup({commit},payload) {
        commit('newRequisiteGroup', payload)
    },
    getRequisiteGroupsList({commit, rootState}){
        axios.get("/requisite-groups", rootState.authentication.accessTokenConfig)
        .then(res => {
            commit('setRequisiteGroups', res.data)
        })
        .catch(error => {
            console.log(error.response);
        })
    },
    setRequisiteGroup({ commit, dispatch, rootState }, data) {
  
        axios.post("/requisite-groups/add", data, rootState.authentication.accessFileTokenConfig )
        .then(res => {
            if(res.status == 201){
                commit('setServerMessage', { message:res.data.message, heading:'New requisite group added', active:true, type:'success' }, { root: true })
                dispatch('getRequisiteGroupsList')
            }
        })
        .catch(error => {
            commit('setServerMessage', { message:error.response.data.message, heading:'New requisite group adding failed!', active:true, type:'error' }, { root: true })
            console.log(error.response);
        })
      },
}


export default {
    state,
    mutations,
    actions,
    getters
}