<template>
  <v-container>
    <v-card class="mx-auto" width="500">
      <div style="padding:30px;width:100%">
        <div class="center">
          <img src="@/assets/logo-cap.svg" height="200px" />
        </div>
        <v-form ref="form" v-model="valid" lazy-validation>
          <div class="center">
            <p
              class="title font-weight-regular"
              style="margin-top:0px"
            >Authenticate here for using Aiila Products</p>
          </div>
          <div style="width:100%">
            <v-text-field
              outlined
              label="Email"
              prepend-inner-icon="email"
              :rules="emailRules"
              required
              v-model="form.email"
            ></v-text-field>
            <v-text-field
              outlined
              label="Password"
              prepend-inner-icon="lock"
              :append-icon="show ? 'mdi-eye-off' : 'mdi-eye'"
              :type="show ? 'text' : 'password'"
              @click:append="show = !show"
              :rules="rules"
              required
              v-model="form.password"
            ></v-text-field>
          </div>
          <v-row align="center">
            <v-col class="text-center" cols="12">
              <v-btn large  color="primary" @click="validate">Submit</v-btn>
            </v-col>
          </v-row>
          <!-- <v-row>
            <v-col>
              <v-alert dismissible :type="authMessage.msg_type" v-if="authMessage.message != null">{{ authMessage.message }}</v-alert>
            </v-col>
          </v-row>-->
        </v-form>
        <v-row align="center">
          <v-col cols="6">
            <router-link class="link" small text to="/auth/reset/request">Forgot Password? Reset here</router-link>
          </v-col>
          <v-col class="text-right" cols="6">
            <router-link class="link" small text to="/auth/register">No account? Register Here</router-link>
          </v-col>
        </v-row>
        <v-row justify="center">
          <v-dialog v-model="authMessage.dialog" persistent max-width="350">
            <v-card>
              <v-card-title
                class="title center"
                style="justify-content:center"
              >{{ authMessage.heading }}</v-card-title>
              <v-card-text class="font-weight-regular center body-1">{{ authMessage.msg }}</v-card-text>
              <v-card-actions style="justify-content:center">
                <v-btn text @click="closeDialog">close</v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-row>
      </div>
    </v-card>
  </v-container>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "Login",
  data() {
    return {
      show: false,
      valid: true,
      form: {
        email: "aiilainnovations@gmail.com",
        password: "1234"
      },
      rules: [value => !!value || "Password Required."],
      emailRules: [
        v => !!v || "E-mail is required",
        v => /.+@.+\..+/.test(v) || "E-mail must be valid"
      ]
    };
  },
  methods: {
    validate() {
      if (this.$refs.form.validate()) {
        this.$store.dispatch("login", {
          email: this.form.email,
          password: this.form.password
        });
      }
    },
    closeDialog() {
      if (this.authMessage.type == "success") this.$refs.form.reset();
      this.$store.dispatch("resetMessage");
    }
  },
  computed: {
    ...mapGetters(["authMessage"])
  }
};
</script>

<style scoped>
.center {
  text-align: center;
}

.link {
  text-transform: Capitalize;
  text-decoration: none;
  font-weight: 400;
  font-size: 12px;
}

h5 {
  font-weight: 400;
}
</style>
