<template>
  <div class="ing_types" style="width:inherit">
    <div class="heading d-flex">
      <div>
        <v-card
          color="accent"
          height="65px"
          width="65px"
          class="d-flex justify-center align-center"
        >
          <img height="35px" width="35px" src="@/assets/icons/ingredients-white.svg" />
        </v-card>
      </div>
      <div style="width:100%;margin-left:10px">
        <v-card height="65px">
          <v-card-title>Cocktails Library</v-card-title>
        </v-card>
      </div>
      <div style="margin-left:10px">
        <v-card
          color="accent"
          height="65px"
          width="65px"
          class="d-flex justify-center align-center"
          @click="toggleView"
        >
          <v-icon color="white">{{ list? 'format_list_bulleted':'apps' }}</v-icon>
        </v-card>
      </div>
    </div>
    <div class="content">
      <v-card class="mt-3 pa-3 pb-0">
        <v-row class="pb-0">
          <v-col class="pb-0">
            <v-text-field
              label="Search Cocktails"
              v-model="search"
              dense
              outlined
              append-icon="search"
            ></v-text-field>
          </v-col>

          <v-col class="pb-0">
            <v-select
              :items="cocktailTypes"
              dense
              item-text="type_name"
              item-value="id"
              label="Select Cocktails Type"
              outlined
            ></v-select>
          </v-col>
          <v-col class="pb-0"></v-col>
          <v-col class="pb-0"></v-col>
          <v-col class="pb-0"></v-col>
        </v-row>
      </v-card>
      <v-card v-if="list" class="mt-3">
        <v-data-table
          :search="search"
          :headers="headers"
          :items="cocktails"
          :items-per-page="10"
          :single-expand="singleExpand"
          :expanded.sync="expanded"
          item-key="cocktail_name"
          show-expand
        >
          <template v-slot:item.image="{ item }">
            <div class="p-2">
              <v-img
                :src="item.img_uri"
                :alt="item.ing_name"
                height="40px"
                width="40px"
                style="border-radius:20px;"
              ></v-img>
            </div>
          </template>
          <template v-slot:expanded-item="{ headers, item }">
            <td :colspan="headers.length" style="padding:0px">
              <v-card class="ma-4">
                <v-simple-table>
                  <template v-slot:default>
                    <thead>
                      <tr>
                        <th colspan="8" class="text-center">Requisites</th>
                      </tr>
                      <tr>
                        <th>Name</th>
                        <th>Quantity</th>
                        <th>Type</th>
                        <th>Group</th>
                        <th>Dispensable</th>
                        <th>Dispensing factor</th>
                        <th>Priority</th>
                        <th>Description</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="(i, index) in item.cocktail_ingredients" :key="index">
                        <td>{{ i.ing_name }}</td>
                        <td>{{ i.ing_quantity }}{{i.ing_units}}</td>
                        <td>{{ i.ing_type_name }}</td>
                        <td>{{ i.ing_gen_type_name }}</td>
                        <td>{{ i.dispensable }}</td>
                        <td>{{ i.disp_factor }}</td>
                        <td>{{ i.ing_priority }}</td>
                        <td>{{ i.ing_desc }}</td>
                      </tr>
                    </tbody>
                  </template>
                </v-simple-table>
              </v-card>
            </td>
          </template>
        </v-data-table>
      </v-card>
      <div v-else>
        <v-row>
          <v-col v-for="item in cocktails" :key="item.name" cols="12" sm="6" md="3" lg="3">
            <v-card>
              <v-img :src="item.img_uri" height="250px" style="background:rgb(0,0,0,.1)"></v-img>
              <v-card-title class="subheading font-weight-bold">{{ item.cocktail_name }}</v-card-title>
              <v-card-subtitle>{{item.cocktail_type_name}}</v-card-subtitle>
              <v-list dense>
                <v-list-item>
                  <v-list-item-content>Description:</v-list-item-content>
                  <v-list-item-content class="align-end">{{ item.cocktail_desc }}</v-list-item-content>
                </v-list-item>

                <v-list-item>
                  <v-list-item-content>Procedure:</v-list-item-content>
                  <v-list-item-content class="align-end">{{ item.cocktail_procedure }}</v-list-item-content>
                </v-list-item>

                <v-list-item>
                  <v-list-item-content>Ingredients:</v-list-item-content>
                </v-list-item>
                <v-list-item>
                  <v-list-item-content>
                    <p v-for="(ing,index) in item.cocktail_ingredients" :key="ing.id">
                      <span>{{index+1}}. {{ing.ing_name}}({{ing.ing_type_name}}) - {{ing.ing_quantity}}{{ing.ing_units}}</span>
                    </p>
                  </v-list-item-content>
                </v-list-item>
              </v-list>
              <v-divider></v-divider>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn text>Edit</v-btn>
              </v-card-actions>
            </v-card>
          </v-col>
        </v-row>
      </div>
    </div>
   
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "UserCocktails",
  data() {
    return {
      show: false,
      expanded: [],
      singleExpand: false,
      search: "",
      list: true,
      dialog: false,
      valid: true,
      url: null,
      file: null,
      image_error: false,
      priority_count: 1,
      alertDialog:false,
      headers: [
        { text: "Image", value: "image", sortable: false },
        { text: "Cocktail Name", value: "cocktail_name", sortable: false },
        { text: "Cocktail Type", value: "cocktail_type_name" },
        { text: "Description", value: "cocktail_desc" },
        { text: "Procedure", value: "cocktail_procedure" },
        { text: "", value: "data-table-expand", sortable: false }
      ],
      
      form: {
        ct_name: "",
        ct_type_id: null,
        ct_procedure: "",
        ct_desc: "",
        img_uri: '',
        ing_list: [
        ]
      },
      rules: {
        ct_name: [value => !!value || "Cocktail name required."],
        ct_type_id: [value => !!value || "Cocktail Type required."],
        ct_procedure: [value => !!value || "Cocktail preperation required."],
        ct_desc: [value => !!value || "Cocktail description required."],
        img_uri:  [value => !!value || "Cocktail image required."],
        ing_list:{
          ing_type_id:[value => !!value || "Requisite type required."],
          ing_id:[value => !!value || (value == 0) || "Requisite required."],
          ing_quantity: [value => !!value || "Requisite quantity required."],
          ing_priority: [value => !!value || "Requisite priority required."]
        }
      },
      temp_var:null
    };
  },
  methods: {
    toggleView() {
      this.list = !this.list;
    },
    addNew() {
      this.dialog = true;
    },
    openFileDialog() {
      document.getElementById("file").click();
    },
    onFileChange(e) {
      this.file = e;
      this.url = URL.createObjectURL(e);
      console.log(e);
    },
    getImgUrl(url) {
      if (url == null) {
        return require("@/assets/image_placeholder.png");
      } else {
        return url;
      }
    },
    // priorityCounter(){
    //     if(this.form.ct_type_id == 1){
    //       return this.priority_count++;
    //     }
    //      if(this.form.ct_type_id == 2){
    //       return this.priority_count;
    //     }
    // },
    removeItem(index) {
      // this.priority_count--
      if (index > -1 && this.form.ing_list[index].ing_type_id!=13) {
        this.form.ing_list.splice(index, 1);
      }
    },
    addItem() {
      this.form.ing_list.push({
        ing_type_id:null,
        ing_id: null,
        ing_quantity: null,
        ing_priority: null,
        ing_desc: ""
      });
    },
     newItem() {
      if (this.$refs.form.validate()) {
        let image = {
          name: this.form.ct_name + this.form.ct_type_id,
          file:this.file
        }
        this.form.img_uri = this.form.ct_name + this.form.ct_type_id
        this.$store.dispatch("setCocktail", { cocktail:this.form, image:image });
      } else {
        if(!this.file){
          this.image_error=true
        }
      }
    },
    updateList(){
     
      this.form.ing_list = [
        { ing_type_id:13, ing_id: null, ing_quantity: 1, ing_priority: 1, ing_desc: "" },
      ]
      this.filterCtType(this.form.ct_type_id).actions.forEach(element => {
        let stype = this.ingredients.filter( test => {
         if(test.id == element.req_id)
         return test
        })
        this.form.ing_list.push({
          ing_type_id:stype[0].ing_type_id, ing_id: element.req_id, ing_quantity: null, ing_priority: 1, ing_desc: ""
        })
      });
       this.form.ing_list.push({
          ing_type_id:null, ing_id: null, ing_quantity: null, ing_priority: 1, ing_desc: ""
        })
    },

    filterCtType(id){
       let ctype =  this.getCocktailTypes.filter(type=> {
        if(type.id == id) {
          return type
        }
      })
      return ctype[0]
    }

  },
  computed: {
    ...mapGetters([
      "getCocktails",
      "getCocktailTypes",
      "getIngredients",
      "getIngredientTypes",
      "getServerMessage",
    ]),
    cocktails() {
      return this.getCocktails;
    },
    cocktailTypes() {
      const SelectValue = ["All"];
      return SelectValue.concat(this.getCocktailTypes);
    },
    ingredients() {
      const SelectValue = [{
        id: 0,
        ing_name: "Any"
      }];
      return SelectValue.concat(this.getIngredients);
    },
    ing_types() {
      return this.getIngredientTypes
    },
  },
  created() {
    if (this.getIngredients.length == 0) {
      this.$store.dispatch("getIngredientsList");
    }
    if (this.getCocktails.length == 0) {
      this.$store.dispatch("getCocktailsList");
      console.log(this.getCocktails);
    }
    if (this.getCocktailTypes.length == 0) {
      this.$store.dispatch("getCocktailTypesList");
      console.log(this.getIngredientTypes);
    }
    if (this.getIngredientTypes.length == 0) {
      this.$store.dispatch("getIngredientsTypesList");
    }
  },
  watch: {
    getServerMessage: function() {
      if (this.dialog) {
        if (
          this.getServerMessage.active &&
          this.getServerMessage.type === "success"
        ) {
          this.dialog = false;
          this.$refs.form.reset();
        }
      }
    }
  }
};
</script>
