import Vue from "vue";
import VueRouter from "vue-router";
import Web from "../views/web.vue";
import WebHome from "../views/web/home.vue"
import WebAbout from "../views/web/about.vue"
import WebServices from "../views/web/services.vue"
import WebProducts from "../views/web/products.vue"
import WebContact from "../views/web/contact.vue"
import ProBartiny from "../views/web/pro-bartiny.vue"

import Auth from "../views/auth.vue";
import Login from "../views/auth/login.vue";
import Register from "../views/auth/register.vue";
import Reset from "../views/auth/reset.vue";
import ResetRequest from "../views/auth/reset-request.vue";
import Logout from "../views/auth/logout.vue";
import Terms from "../views/auth/terms.vue"
import Invitation from "../views/auth/invitation.vue"
import Confirm from "../views/auth/confirm.vue"
import ResendConfirm from "../views/auth/resend-confirm.vue"

import Admin from "../views/admin.vue";
import Overview from "../views/admin/overview.vue";
import Devices from "../views/admin/devices/devices.vue";
import Bartiny from "../views/admin/devices/bartiny.vue";
import Products from "../views/admin/products.vue";
import DeviceDetails from "../views/admin/devices/device-details.vue";
import Requisites from "../views/admin/requisites/requisites.vue";
import RequisiteTypes from "../views/admin/requisites/requisite-types.vue";
import RequisiteGroups from "../views/admin/requisites/requisite-groups.vue";
import Cocktails from "../views/admin/cocktails/cocktails.vue";
import CocktailTypes from "../views/admin/cocktails/cocktail-types.vue";
import Users from "../views/admin/users/users.vue";
import SuperUsers from "../views/admin/users/superusers.vue";
import Support from "../views/admin/support.vue"
import Settings from "../views/admin/settings.vue"
import DeviceAnalytics from "../views/admin/analytics/device-analytics.vue"
import DispenseAnalytics from "../views/admin/analytics/dispense-analytics.vue"

import User from "../views/user.vue";
import UserOverview from "../views/user/overview.vue";
import UserDevices from "../views/user/user-devices.vue";
import UserCocktails from "../views/user/cocktails.vue";
import UserSupport from "../views/user/support.vue"
import UserSettings from "../views/user/settings.vue"
import UserAnalytics from "../views/user/analytics.vue"
import UserInventory from "../views/user/inventory.vue"
import UserRequisites from "../views/user/user-requisites.vue";

import store from '../store/store';


Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    components: {
      default:Web
    },
    children : [
      { path: "", name:"WebHome", component: WebHome },
      { path: "about", name:"WebHome", component: WebAbout },
      { path: "services", name:"WebServices", component: WebServices },
      { path: "products", name:"WebProducts", component: WebProducts },
      { path: "contact", name:"WebContact", component: WebContact },
      { path: "bartiny", name:"Bartiny", component: ProBartiny },
    ] 
  },
  {
    path: "/auth",
    components: {
      default:Auth
    },
    children : [
      { path: "", component: Login },
      { path: "register", name:"Register", component: Register },
      { path: "reset/request", name:"ResetRequest", component: ResetRequest },
      { path: "reset/:id", name:"Reset", component: Reset },
      { path: "logout", name:"Logout", component: Logout },
      { path: "terms", name:"Terms", component: Terms },
      { path: "resend", name:"ResendConfirm", component: ResendConfirm },
      { path: "confirm/:cid", name:"Confirm", component: Confirm  },
      { path: "invitation/:id", name:"Invitation", component: Invitation  },
    ] 
  },
  {
    path: "/user",
    name: "User",
    components:{
      default: User,
    },
    children:[
      { path: "", name:"UserOverview", component: UserOverview },
      { path: "devices", name:"UserDevices", component: UserDevices },
      { path: "requisites", name:"UserRequisites", component: UserRequisites },
      { path: "cocktails", name:"UserCocktails", component: UserCocktails },
      { path: "support", name:"UserSupport", component: UserSupport },
      { path: "settings", name:"UserSettings", component: UserSettings },
      { path: "analytics", name:"UserAnalytics", component: UserAnalytics },
      { path: "inventory", name:"UserInventory", component: UserInventory }
    ]
  },
  {
    path: "/admin",
    components: {
      default: Admin,
      "inner": Overview,
    },
    beforeEnter (to, from, next) {
      store.dispatch("tryAutoLogin");
      console.log(store.getters.isAuthenticated)
      if (store.getters.isAuthenticated) {
        next()
      } else {
        next('/auth')
      }
    },
    children: [
      { path: "", name:"Overview", component: Overview },
      { path: "devices", name:"Devices", component: Devices },
      { path: "devices/bartiny", name:"Bartiny", component: Bartiny },
      { path: "products", name:"Products", component: Products },
      { path: "device/details/:id", name:"DeviceDetails", component: DeviceDetails },
      { path: "requisites", name:"Requisites", component: Requisites },
      { path: "requisites/types", name:"RequisiteTypes", component: RequisiteTypes },
      { path: "requisites/groups", name:"RequisiteGroups", component: RequisiteGroups },
      { path: "cocktails", name:"Cocktails", component: Cocktails },
      { path: "cocktails/types", name:"CocktailTypes", component: CocktailTypes },
      { path: "users", name:"Users", component: Users },
      { path: "superusers", name:"SuperUsers", component: SuperUsers },
      { path: "support", name:"Support", component: Support },
      { path: "settings", name:"Settings", component: Settings },
      { path: "analytics/devices", name:"DeviceAnalytics", component: DeviceAnalytics },
      { path: "analytics/dispenses", name:"DispenseAnalytics", component: DispenseAnalytics },
    ],

  
  },
  // {
  //   path: "/requisites",
  //   name: "Requisites",
  //   // route level code-splitting
  //   // this generates a separate chunk (about.[hash].js) for this route
  //   // which is lazy-loaded when the route is visited.
  //   component: () =>
  //     import(/* webpackChunkName: "about" */ "../views/requisites.vue")
  // }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

export default router;
